.RegistrationForm{
    margin-top: 227px;
    @media(max-width: 1200px){
        margin-top: 100px;
    }
    &__title-container{
        &__title{
            color: var(--registration-title);
            font-family: 'DM Sans', sans-serif;
            font-size: 36px;
            font-weight: 700;
            line-height: 56px; 
            letter-spacing: -0.72px;
            @media(max-width: 1200px){
                text-align: center;
            }
        }
        &__subtitle{
            color: var(--nav-list-disable);
            font-family: 'DM Sans', sans-serif;
            font-size: 16px;
            font-weight: 400;
            line-height: 100%;
            letter-spacing: -0.32px;
            margin-top: 6px;
            @media(max-width: 1200px){
                text-align: center;
            }
        }
    }
   
}
.column{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.submitButton{
    background-color: #2E016C;
    color: #FFF;
    padding: 15px 25px;
    border-radius: 20px;
    &:hover{
        color: #2E016C;
    }
}
.columns{
    display: flex;
    flex-direction: row;
    margin-left: 50px;
    @media(max-width: 1880px){
        margin-right: 50px;
        margin-left: 0px;
    }
    @media(max-width: 1200px){
        flex-direction: column;
        margin-right: 0px;
    }
    @media(max-width: 1000px){
        flex-direction: row;
    }
    @media(max-width: 670px){
        flex-direction: column;
    }
}
.c-2{
    margin-left: 22px;
    @media(max-width: 1200px){
        margin-left: 0px;
    }
    @media(max-width: 1000px){
        margin-left: 22px;
    }
    @media(max-width: 670px){
        margin-left: 0px;
    }
}
.form{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.checkBox{
    display: flex;
    align-items: center;
    margin-top: 54px;
    h4{
        color: var(--blue-first);
        font-family: 'DM Sans', sans-serif;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        letter-spacing: -0.28px;
        margin-left: 11px;
        margin-bottom: 0px;
    }
}
.submitButton{
    max-width: 395px;
    width: 100%;
    margin-top: 33px;
}
.purple-text{
    color: #19033B;
    font-family: 'DM Sans', sans-serif;
    font-size: 14px;
    font-weight: 700;
    line-height: 26px;
    letter-spacing: -0.28px;
    a{
        text-decoration: none;
        cursor: pointer;
    }
}

.foot{
    color: var(--blue-first);
    font-family: 'DM Sans', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 26px;
    margin-top: 26px;
    letter-spacing: -0.28px;
    margin-bottom: 0px;
}
