.StatusScriptSend {
  border-radius: 15px;
  background: rgba(165, 165, 165, 0.07);
  max-width: 640px;
  width: 100%;
  padding-left: 41px;
  padding-top: 22px;
  padding-right: 26px;
  margin-left: 67px;
  min-height: 556px;
  margin-top: 20px;
}
@media (max-width: 1670px) {
  .StatusScriptSend {
    margin-left: 0px;
    width: 100%;
  }
}
.StatusScriptSend__status-color {
  border-radius: 15px;
  padding-bottom: 5px;
  padding-top: 5px;
  max-width: 342px;
  width: 100%;
  display: flex;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.24);
  box-shadow: 0px 0px 11px 0px rgba(0, 0, 0, 0.25);
}
.StatusScriptSend__status-color__dot {
  background: black;
  width: 6px;
  height: 6px;
  border-radius: 100%;
  margin-left: 16px;
}
.StatusScriptSend__status-color__text {
  margin-left: 18px;
  color: #000;
  font-family: "Open Sans";
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: -0.48px;
}

.StatusScriptSend__script h2 {
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.28px;
  color: var(--text-color-secondary);
  margin-top: 30px;
  max-width: 462px;
  width: 100%;
}
.StatusScriptSend__script h2 a {
  color: var(--text-color-secondary);
  font-weight: 700;
}
.StatusScriptSend__script__title {
  color: var(--text-color-secondary);
  font-family: "Open Sans";
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: -0.48px;
  margin-top: 31px;
}
.StatusScriptSend__script__body {
  margin-top: 17px;
  width: 100%;
  min-height: 165px;
  border-radius: 15px;
  border: 1px solid var(--nav-list-disable);
  background: var(--background-color-first);
}
.StatusScriptSend__script__body__text {
  color: var(--text-color-secondary);
  font-family: "Open Sans";
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: -0.3px;
  padding-left: 26px;
  padding-top: 17px;
  padding-right: 19px;
  padding-bottom: 10px;
}

.StatusScriptSend__script__buttons {
  display: flex;
  align-items: center;
  margin-top: 17px;
}
@media (max-width: 500px) {
  .StatusScriptSend__script__buttons {
    flex-direction: column;
  }
}
.StatusScriptSend__script__buttons__button {
  margin-left: 19px;
  margin-top: 15px;
}
@media (max-width: 500px) {
  .StatusScriptSend__script__buttons__button {
    margin-left: 0px;
  }
}
.StatusScriptSend__script__buttons__button:first-child {
  margin-left: 0px;
}/*# sourceMappingURL=StatusScriptSend.css.map */