.RevenueSchedule{
    margin-left: 20px;
    position: relative;
    margin-top: 50px;
    @media(max-width: 1800px) {
        margin-left: 0px;
    }
    @media(max-width: 1100px) {
        display: none;
    }
    &::before{
        content: '';
        border-radius: 437px;
        background: linear-gradient(180deg, #183398 0%, #322B54 70.14%);
        filter: blur(300px);
        width: 437px;
        height: 437px;
        position: absolute;
        left: 0px;
        top: 0px;
    }
    &__header{
        display: flex;
        align-items: center;
        h3{
            white-space: nowrap;

            color: #FFF;
            font-family: 'Open Sans';
            font-size: 39px;
            font-style: normal;
            font-weight: 600;
            line-height: normal;
            letter-spacing: 1.17px;
            @media(max-width: 1600px) {
               font-size: 30px;
            }
            @media(max-width: 1300px) {
                font-size: 28px;
            }
            @media(max-width: 1250px) {
                font-size: 24px;
            }
        }
        .month{
            white-space: nowrap;

            display: flex;
            align-items: center;
            margin-left: 65px;
            h4{
                color: var(--primary-purple, #AB71F4);
                text-align: center;
                font-family: 'Manrope';
                font-size: 27px;
                font-style: normal;
                font-weight: 600;
                @media(max-width: 1600px) {
                    font-size: 25px;
                }
                @media(max-width: 1250px) {
                    font-size: 22px;
                }
                line-height: normal;
            }
            &__icon{
                background: url('../../../../assets/landing/images/arrow-down.svg') no-repeat center center / cover;
                height: 12px;
                width: 22px;
                margin-left: 13px;
            }
        }
        h5{
            white-space: nowrap;

            margin-left: 194px;
            text-align: center;
            font-family: 'Manrope';
            font-size: 27px;
            font-style: normal;
            font-weight: 600;
            line-height: normal;
            letter-spacing: 2.565px;
            color: #FFF;
            @media(max-width: 1600px) {
                margin-left: 100px;
            }
            @media(max-width: 1250px) {
                font-size: 24px;
                margin-left: 50px;
            }
        }
    }
    &__img{
        margin-top: 40px;
        max-width: 782px;
        width: 100%;
        position: relative;
        @media(max-width: 1600px) {
            max-width: 650px;
        }
        @media(max-width: 1250px) {
            max-width: 525px;
        }
        img{
            width: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
    &__bottom-bar{
        margin-top: 113px;
        display: flex;
        justify-content: space-between;
        &__total{
            white-space: nowrap;
            h2{
                color: #FFF;
                font-family: 'Manrope';
                font-size: 64px;
                font-style: normal;
                font-weight: 700;
                line-height: normal;
                @media(max-width: 1500px) {
                    font-size: 50px;
                }
                @media(max-width: 1250px) {
                    font-size: 40px;
                }
            }
            h4{
                margin-top: 36px;
                color: #FFF;
                font-family: 'Open Sans';
                font-size: 32px;
                font-style: normal;
                font-weight: 600;
                line-height: normal;
                @media(max-width: 1500px) {
                    font-size: 28px;
                }
            }
            h3{
                margin-top: 4px;
                color: #7C7C7C;
                font-family: 'Manrope';
                font-size: 20px;
                font-style: normal;
                font-weight: 600;
                line-height: normal;
                letter-spacing: 1.9px;
            }
        }
        &__percent{
            white-space: nowrap;
            
            &__mini-graf_icon{
                background: url('../../../../assets/landing/images/mini-graph.svg') no-repeat center center / cover;
                width: 173px;
                height: 80px;
            }
            h4{
                margin-top: 43px;
                color: #0BAF07;
                font-family: 'Manrope';
                font-size: 32px;
                font-style: normal;
                font-weight: 600;
                line-height: normal;
                text-align: right;
            }
            h3{
                margin-top: 4px;
                color: #7C7C7C;
                font-family: 'Manrope';
                font-size: 23px;
                font-style: normal;
                font-weight: 600;
                line-height: normal;
                letter-spacing: 2.185px;
                text-align: right;
            }
        }
    }
}