.Laptop_marquee-item {
  font-family: "RoadRadio", sans-serif;
  font-weight: 900;
  line-height: normal;
  text-transform: uppercase;
  background: linear-gradient(180deg, #CF83FF 0%, #6A4BFF 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-left: 5vw;
}

.Laptop__img {
  position: relative;
}
@media (max-width: 500px) {
  .Laptop__img {
    margin-top: 50px;
  }
}
.Laptop__img img {
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: center center;
     object-position: center center;
}
.Laptop__img__blob-container {
  transform: rotate(15.541deg);
  position: absolute;
  right: 100px;
  bottom: 0px;
}
.Laptop__img::before {
  content: "";
  background: url("../../../../assets/landing/images/mobail.png") no-repeat center center/cover;
  height: 94%;
  width: 50%;
  position: absolute;
  left: 0px;
  top: -5%;
  z-index: 99;
}/*# sourceMappingURL=Laptop.css.map */