.LastContact{
    padding-left: 35px;
    padding-top: 29px;
    padding-right: 32px;
    padding-bottom: 74px;
    max-width: 820px;
    width: 100%;
    margin-top: 20px;
    background-color: var(--background-color-four);
    border-radius: 20px;
    box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
    @media(max-width: 1400px) {
        max-width: 100%;
    }
}
.LastContact-icon{
    background: url('../../../../assets/account/images/revenuaContacts.svg') no-repeat center center / cover;
}

.LastContact__body{
    display: flex;
    margin-top: 11px;
    justify-content: space-between;
    @media(max-width: 700px) {
        justify-content: center;
        flex-direction: column;
        align-items: center;
        margin-top: 27px;
    }
    &__contactAndButtons{
        &__contact{
            display: flex;
            align-items: center;
            &__avatar{
                width: 100px;
                border-radius: 100%;
                img{
                    width: 100%;
                    border-radius: 100%;
                    object-fit: cover;
                    object-position: center center;
                }
            }
            &__text{
                margin-left: 12px;
                h5{
                    color: var(--nav-list-disable);
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 500;
                    line-height: 24px;
                    letter-spacing: -0.28px;
                    margin-top: 0px;
                    margin-bottom: 0px;
                }
            }
        }
        &__buttons{
            display: flex;
            align-items: center;
            margin-top: 33px;
            @media(max-width: 700px) {
                display: none;
            }
            &__button{
                padding: 6px 20px;
                background: var(--ref-button);
                border-radius: 70px;
                margin-left: 14px;
                min-width: 106px;
                cursor: pointer;

                color: #FFF;
                text-align: center;
                font-size: 14px;
                font-style: normal;
                font-weight: 500;
                line-height: 24px;
                letter-spacing: -0.28px;

                &:first-child{
                    margin-left: 0px;
                }
            }
        }
    }
    
}

.speedTest{
    margin-right: 20px;
    width: 276px;
    height: 196px;
    background: url('../../../../assets/account/images/speedometer.png') no-repeat center center / cover;
    @media(max-width: 1580px) {
        width: 200px;
        height: 130px;
    }
    @media(max-width: 700px) {
        margin-top: 11px;
        margin-right: 0px;
    }
}
.LastContact__body__contactAndButtons__buttons_disable{
    display: none;
    @media(max-width: 700px) {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 24px;
    }
    @media(max-width: 460px){
        flex-direction: column;
        align-items: center;
    }
}
#img-LastContact-cont{
    position: relative;
    &::before{
        content: '';
        background: url('../../../../assets/account/images/avatar-psevdo-image.png') no-repeat center center / cover;
        width: 26px;
        height: 26px;
        position: absolute;
        right: 0px;
        bottom: 0px;
        cursor: pointer;
    }
}

