.ChangeStrategy{
    padding-left: 35px;
    padding-top: 20px;

    padding-right: 32px;
    padding-bottom: 74px;
    max-width: 425px;
    margin-top: 20px;
    width: 100%;
    background-color: var(--background-color-four);
    border-radius: 20px;
    box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
    @media(max-width: 1670px) {
        margin-left: 0px;
        max-width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
}
.ChangeStrategy__body__status{
    margin-top: 4px;
}
.ChangeStrategy-icon{
    @media(max-width: 1670px) {
        display: none;
    }
}
.ChangeStrategy__body{
    padding-left: 31px;
    padding-right: 31px;
    @media(max-width: 1670px) {
        padding-left: 10px;
        padding-right: 10px;
    }
}
.ChangeStrategy__dropdownItem{
    max-width: 320px;
    @media(max-width: 1860px) {
        max-width: 800px;
    }
    width: 100%;
    height: 48px;
    text-align: left;
    position: relative;
    background-color: transparent;
    font-family: 'Inter';
    color: #FFFFFF;
    border: 1px solid #A3AED0;
    &::after{
        content: '';
        display: block;
        vertical-align: 0px;
        border-top: 0px;
        border-bottom: 0px;
        border-right: 0px;
        border-left: 0px;
        transition: all 0.4s;
        position: absolute;
        right: 14px;
        top: 12px;
        transform: rotate(-180deg);
        background: url('../../../../assets/account/images/arrow-select.svg') no-repeat center center / cover;
        width: 20px;
        height: 20px;
    }
    @media(max-width: 1670px){
        max-width: 635px;
    }
}
.dropdown-item{
    &:hover{
        background-color: var(--background-color-seven-hover);
    }
}
.ChangeStrategy__dropdown-menu{
    background-color: var(--background-color-seven);
    border-radius: 8px;
    box-shadow: 0px 4px 6px -2px rgba(16, 24, 40, 0.03), 0px 12px 16px -4px rgba(16, 24, 40, 0.08);
    
}
.ChangeStrategy__dropdownItem_active{
    max-width: 320px;
    @media(max-width: 1860px) {
        max-width: 800px;
    }
    width: 100%;
    height: 48px;
    text-align: left;
    font-family: 'Inter';
    position: relative;
    background-color: transparent;
    color: #F9FAFB;
    border: 1px solid #A3AED0;
    &::after{
        content: '';
        display: block;
        vertical-align: 0px;
        border-top: 0px;
        border-bottom: 0px;
        border-right: 0px;
        border-left: 0px;
        position: absolute;
        right: 14px;
        top: 12px;
        transition: all 0.4s;
        transform: rotate(0deg);
        background: url('../../../../assets/account/images/arrow-select.svg') no-repeat center center / cover;
        width: 20px;
        height: 20px;
    }
    @media(max-width: 1670px){
        max-width: 635px;
    }
}
.ChangeStrategy__body__dropdown{
    margin-top: 22px;
}



.btn{
    color: var(--nav-list-disable);
    background-color: transparent;    
    border-color: var(--nav-list-disable);    
}
.btn:hover{
    color: var(--nav-list-disable);
    background-color: transparent;    
    border-color: var(--nav-list-disable);    
}
.btn.show{
    color: var(--nav-list-disable);
    background-color: transparent;    
    border-color: var(--nav-list-disable);
}
.btn:first-child:active{
    color: var(--nav-list-disable);
    background-color: transparent;    
    border-color: var(--nav-list-disable);
}
.btn.show:first-child:active{
    color: var(--nav-list-disable);
    background-color: transparent;    
    border-color: var(--nav-list-disable);
}
.dropdown-item:active{
    color: var(--nav-list-disable);
    background-color: transparent;    
    border-color: var(--nav-list-disable);
}
.ChangeStrategy__dropdown-item{
    font-family: 'Inter';
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    position: relative;
    padding-top: 10px;
    padding-bottom: 10px;
    @media(max-width: 1670px){
        max-width: 635px;
    }
    @media(max-width: 500px){
        font-size: 13px;
    }
}
.ChangeStrategy__dropdown-item_active{
    font-family: 'Inter';
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    @media(max-width: 500px){
        font-size: 13px;
    }
    position: relative;
    padding-top: 10px;
    padding-bottom: 10px;
    &::after{
        content: '';
        position: absolute;
        right: 14px;
        top: 12px;
        width: 20px;
        height: 20px;
        background: url('../../../../assets/account/images/galochka.svg') no-repeat center center / cover;
        @media(max-width: 500px){
            width: 12px;
            height: 12px;
        }
    }
    @media(max-width: 1670px){
        max-width: 635px;
    }
}
.ChangeStrategy__body__speed{
    &__title{
        margin-top: 145px;
        color: #2B3674;
        text-align: center;
        font-family: 'Open Sans';
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 24px;  
        letter-spacing: -0.36px;
        
    }
    &__speedTest{
        margin-top: 25px;
    }
}
.ChangeStrategy__blue-buttons{
    max-width: 283px;
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 16px;
    & > div{
        margin-left: 23px;
        &:first-child{
            margin-left: 0px;
        }
    }
}

.dropdown-item:focus{
    background-color: initial;
} 