.ProfileMotivator {
  background: url("../../../../assets/account/images/champion.png") no-repeat center right/cover;
  width: 100%;
  min-height: 350px;
  padding-left: 31px;
  padding-top: 131px;
  padding-bottom: 131px;
  border-radius: 20px;
  margin-top: 26px;
  display: flex;
  justify-content: left;
  align-items: center;
  margin-left: 20px;
}
@media (max-width: 1400px) {
  .ProfileMotivator {
    margin-left: 0px;
    background: url("../../../../assets/account/images/champion.png") no-repeat center left/cover;
  }
}
@media (max-width: 480px) {
  .ProfileMotivator {
    padding-left: 10px;
    padding-right: 10px;
  }
}
.ProfileMotivator__title {
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 42px;
  margin-bottom: 0px;
  letter-spacing: -0.68px;
  color: var(--text-color-first);
}
@media (max-width: 480px) {
  .ProfileMotivator__title {
    text-align: center;
  }
}/*# sourceMappingURL=ProfileMotivator.css.map */