.MarketItem{
    min-width: 350px;
    max-width: 381px;
    background: var(--background-color-four);
    border-radius: 20px;
    padding: 22px 21px;
    margin-left: 20px;
    width: 100vw;
    &:first-child{
        margin-left: 0px;
    }
    &__img{
        height: 205px;
        width: 100%;
        border-radius: 18px;
        img{
            object-fit: cover;
            width: 100%;
            height: 100%;
            border-radius: 18px;
        }
    }
    &__text{
        padding-left: 9px;
        margin-top: 19px;
        h2{
            font-size: 18px;
            font-style: normal;
            font-weight: 700;
            line-height: 30px; 
            letter-spacing: -0.36px;
            color: var(--blue-secondary);
        }
        h4{
            color: var(--nav-list-disable);
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 20px;
            letter-spacing: -0.28px;
            margin-top: 5px;
        }
    }
    &__bottom-bar{
        padding-left: 9px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 20px;
        padding-right: 8px;
        h3{
            font-size: 14px;
            font-style: normal;
            font-weight: 700;
            line-height: 24px;
            letter-spacing: -0.28px;
            color: var(--text-color-three);
        }
    }
}