.ContactItem:first-child {
  margin-top: 8px;
}
.ContactItem__row {
  display: flex;
  width: 100vw;
  align-items: center;
  cursor: pointer;
  background-color: var(--background-color-seven);
}
.ContactItem__row:hover {
  background: var(--background-color-three);
  box-shadow: -50px 0 0 0 var(--bg-light), 0 0 0 0 var(--bg-light), 200px 0 0 0 var(--bg-light), 400px 0 0 0 var(--bg-light), 600px 0 0 0 var(--bg-light), 800px 0 0 0 var(--bg-light), 1000px 0 0 0 var(--bg-light);
}
.ContactItem__row__cell {
  width: 100%;
  min-width: 200px;
}
.ContactItem__row__cell:first-child {
  padding-left: 5px;
}
.ContactItem__row__cell h4 {
  white-space: nowrap;
  color: var(--blue-first);
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.28px;
  margin-bottom: 0px;
  padding: 9px 0px;
}

.ContactItem__row:hover .ContactItem__row__cell {
  margin-bottom: 0px;
}
.ContactItem__row:hover .ContactItem__row__cell h4 {
  white-space: nowrap;
  color: var(--text-color-three);
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.28px;
  padding: 9px 0px;
}/*# sourceMappingURL=ContactItem.css.map */