.StrategyWork{
    padding-left: 31px;
    padding-top: 20px;
    padding-right: 56px;
    padding-bottom: 60px;
    margin-top: 20px;
    margin-left: 32px;
    width: 100%;
    background-color: var(--background-color-four);
    border-radius: 20px;
    box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
    @media(max-width: 1670px) {
        margin-left: 0px;
        max-width: 100%;
    }
    &__body{
        display: flex;
        justify-content: space-between;
        @media(max-width: 1670px){
            flex-direction: column;
            justify-content: center;
            width: 100%;
            align-items: center;
        }
    }
}
.StrategyWork__body__status-change{
    max-width: 320px;
    width: 100%;
    margin-top: 4px;
    @media(max-width: 1670px){
        max-width: 635px;
        width: 100%;
    }
}
.StrategyWork__update{
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 10px;
    padding-right: 10px;
    color: #FF6464;
    font-family: 'Open Sans';
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px; 
    letter-spacing: -0.28px;
    border-radius: 10px;
    background: var(--background-color-secondary);
}
.StrategyWork__head{
    @media(max-width: 700px){
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}