.EditContact {
  padding-left: 31px;
  padding-top: 20px;
  padding-right: 25px;
  padding-bottom: 41px;
  width: 100%;
  margin-top: 20px;
  background-color: var(--background-color-four);
  border-radius: 20px;
  box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
}
@media (max-width: 1400px) {
  .EditContact {
    max-width: 100%;
  }
}

.EditProfile__dropdown {
  margin-top: 32px;
}

.EditProfile__dropdown__body {
  max-width: 410px;
  width: 100%;
  height: 48px;
  text-align: left;
}

.EditProfile__bottom {
  margin-top: 33px;
}
@media (max-width: 1350px) {
  .EditProfile__bottom {
    display: flex;
    justify-content: center;
  }
}
.EditProfile__bottom__button-cont {
  max-width: 198px;
  width: 100%;
}

.dropdown__title {
  color: var(--blue-first);
  font-size: 14px;
  font-weight: 500;
  line-height: 100%;
  letter-spacing: -0.28px;
  margin-bottom: 0px;
  padding-bottom: 10px;
}/*# sourceMappingURL=EditContact.css.map */