.Videos{
    &__reg-button-cont{
        width: 100%;
        display: flex;
        justify-content: center;
    }
}
.Videos_marquee-item{
    margin-left: 5vw;
    
}

.Videos__players{
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
    @media(max-width:1180px) {
        flex-direction: column;
        justify-content: center;
    }
}
.player-cont{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    h3{
        color: #FFF;
        text-align: center;
        text-shadow: 1px 1px 20px #000;
        font-family: 'Open Sans';
        font-size: 64px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        margin-top: 34px;
        @media(max-width:1720px) {
            margin-top: 40px;
        }
        @media(max-width:1600px) {
            font-size: 35px;
        }
        @media(max-width:1180px) {
            font-size: 25px;
        }
        @media(max-width:900px) {
            font-size: 26px;
        }
        @media(max-width:720px) {
            font-size: 22px;
            margin-top: 20px;
        }
    }
    

}
.Videos__players__player{
    cursor: pointer;    
    max-width:785px;
    overflow: hidden;
    border-radius: 26px;
    border:1px solid #454545;
    @media(max-width:1720px) {
        margin-top: 20px;
    }
    @media(max-width:720px) {
        margin-top: 20px;
    }
    &::after{
        content: '';
        position: absolute;
        left: 0; top: 0;
        width: 482px;
        z-index: -1;
        height: 482px;
        transform: rotate(15.541deg);
        background: linear-gradient(180deg, #eac7ffc7 0%, #6373ffb9 70.14%);
        filter: blur(300px);
        @media(max-width:1100px) {
            width: 250px;
        }
        @media(max-width:450px) {
            width: 0px;
            height: 0px;
            display: none;
        }
    }

    &:first-child{
        margin-left: 0;
    }
}
.videos__players__player_frame{
    width: 785px;
    height: 461px;
    @media(max-width: 1830px) {
        width: 650px;
        height: 360px;
    }
    @media(max-width:1540px) {
        width: 550px;
        height: 320px;
    }
    @media(max-width:1320px) {
        width: 480px;
        height: 280px;
    }
    @media(max-width:700px) {
        width: 380px;
        height: 240px;
    }
    @media(max-width:500px) {
        width: 300px;
        height: 170px;
    }
    @media(max-width: 300px) {
        width: 200px;
        height: 100px;
    }
}