.AccountNavbar{
    padding-top: 5px;
    padding-bottom: 20px;
    width: 100%;
    &__subTitle{
        color: var(--grey-first);
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
    }
}
.AccountNavbar__bar{
    display: flex;
    background: var(--background-color-first);
    align-items: center;
    justify-content: space-between;
    box-shadow: 14px 17px 40px 4px rgba(112, 144, 176, 0.08);
    padding: 10px;
    border-radius: 20px;
    &__settings{
        display: flex;
        align-items: center;
    }
}
.nameAndActive{
    display: flex;
    align-items: center;
}
.page-name{
    color: var(--blue-first);
    font-size: 34px;
    font-style: normal;
    font-weight: 700;
    line-height: 42px;
    letter-spacing: -0.68px;
    @media(max-width: 1110px) {
        font-size: 21px;
    }
    @media(max-width: 956px) {
        display: none;
    }
}
.prm-active{
    color: var(--grey-first);
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px; 
    margin-left: 57px;
    @media(max-width: 1150px) {
        margin-left: 24px;
    }
    @media(max-width: 1035px) {
        display: none;
    }
    
    
}
.button-ref{
    cursor: pointer;
    background-color: var(--ref-button);
    color: var(--text-color-first);
    padding: 7px 15px;
    border-radius: 70px;
    font-size: 14px;
    font-weight: 500;
        transition: all 0.4s;
        line-height: 24px;
    letter-spacing: -0.28px;
    white-space: nowrap;
    &:hover{
        background: var(--blue-button-hover);
        transition: all 0.4s;
    }
    @media(max-width: 1275px) {
        font-size: 12px;
        padding: 5px 10px;
    }
    @media(max-width: 464px) {
        font-size: 10px;
        padding: 5px 5px;
    }
    
}
.isMetaMask__disable{
    width: 27px;
    margin-left: 32px;
    height: 27px;
    background: url('../../../../assets/account/images/metaMaskDisable.png') no-repeat center center / cover;
    @media(max-width: 1110px) {
        margin-left: 20px;
    }
    @media(max-width: 464px) {
        margin-left: 7px;
    }
}
.isMetaMask__active{
    width: 27px;
    margin-left: 32px;
    height: 27px;
    background: url('../../../../assets/account/images/metaMaskActive.png') no-repeat center center / cover;
    @media(max-width: 1110px) {
        margin-left: 20px;
    }
    @media(max-width: 464px) {
        margin-left: 7px;
    }
}
.notification{
    background: url('../../../../assets/account/images/notifications.svg') no-repeat center center / cover;
    width: 22px;
    margin-left: 15px;
    cursor: pointer;
    height: 22px;
    @media(max-width: 464px) {
        margin-left: 7px;
    }
}
.AccountNavbar__bar__settings__theme{
    background: url('../../../../assets/account/images/themeMoon.svg') no-repeat center center / cover;
    width: 18px;
    margin-left: 20px;
    cursor: pointer;
    height: 18px;
    @media(max-width: 464px) {
        margin-left: 7px;
    }
}
.AccountNavbar__bar__settings__info{
    background: url('../../../../assets/account/images/info.svg') no-repeat center center / cover;
    margin-left: 20px;
    cursor: pointer;
    width: 20px;
    height: 20px;
    @media(max-width: 600px) {
        display: none;
    }
}
.AccountNavbar__bar__settings__avatar{
    border-radius: 100%;
    background: gray;
    width: 41px;
    margin-left: 20px;
    cursor: pointer;
    height: 41px;
    @media(max-width: 464px) {
        margin-left: 10px;
    }
    @media(max-width: 386px) {
        width: 25px;
        height: 25px;
    }
    @media(max-width: 360px) {
        display: none;
    }
}
.AccountNavbar__burger-button{
    cursor: pointer;
    background: url('../../../../assets/account/images/burger-but.svg') no-repeat center center / cover;
    height: 56px;
    width: 56px;
    display: none;
    @media(max-width: 956px) {
        display: block;
    }
}
.AccountNavbar__burger-button_dark{
    cursor: pointer;
    background: url('../../../../assets/account/images/burger-icon-dark.png') no-repeat center center / cover;
    height: 56px;
    width: 56px;
    display: none;
    @media(max-width: 956px) {
        display: block;
    }
}