.MobailAdd{
    position: absolute;
    right: 0px;
    @media(max-width: 960px) {
        margin-top: 50px;
    }
    &:before{
        content: '';
        width: 901px;
        position: absolute;
        right: 0px;
        top: 0px;
        z-index: -1;
        height: 720px;
        border-radius: 901px;
        background: linear-gradient(180deg, #7531BA 0%, rgba(137, 78, 196, 0.00) 100%);
        filter: blur(300px);
        @media(max-width: 1400px) {
            width: 700px;
            height: 600px;
        }
    }
    &__img{
        max-width: 903px;
        width: 100%;
        @media(max-width: 1700px) {
            max-width: 600px;
        }
        @media(max-width: 1420px) {
            max-width: 500px;
        }
        
        img{
            width: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
    &__buttons{
        display: flex;
        align-items: center;
        margin-top: 56px;
        @media(max-width: 1420px) {
            justify-content: right;
            padding-right: 20px;
        }
        @media(max-width:960px){
            justify-content: center;
            margin-top: 78px;
        }
        
        &__button{
            max-width: 200px;
            width: 100%;
            margin-left: 60px;
            @media(max-width:1100px){
                margin-left: 25px;
            }
            @media(max-width:1100px){
                margin-left: 60px;
            }
            
            &:first-child{
                margin-left: 0;
            }
            @media(max-width:1400px){
                max-width: 145px;
            }
            &__icon-and-text{
                cursor: pointer;

                width: 100%;
                height: 66px;
                @media(max-width:1400px){
                    height: 54px;
                }
                @media(max-width:975px){
                    height: 45px;
                }
                border-radius: 12px;
                border: 1px solid rgba(255, 255, 255, 0.20);
                background: rgba(126, 126, 126, 0.29);
                backdrop-filter: blur(10px);
                transition: all 0.4s;

                display: flex;
                align-items: center;
                justify-content: center;
                &:hover{
                    transition: all 0.4s;
                    background: rgba(126, 126, 126, 0.644);
                }

                &__icon{
                    width: 40px;
                    height: 40px;
                    
                    
                }
                &__text{
                    margin-left: 12px;
                    color: #FFF;
                    font-family: 'Inter';
                    font-size: 22px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: normal;
                    
                    @media(max-width:1400px){
                        font-size: 18px;
                    }
                    @media(max-width:975px){
                        font-size: 16px;

                    }
                    @media(max-width:800px){
                        font-size: 14px;

                    }
                }
            }
            &__bottom-text{
                color: #FFF;
                font-family: 'Open Sans';
                font-size: 23px;
                font-style: normal;
                font-weight: 400;
                line-height: 70px;
                text-align: center;
                @media(max-width:1400px){
                    font-size: 18px;
                }
            }
        }
    }
}
#market{
    background: url('../../../../assets/landing/images/gPlay.svg') no-repeat center center / cover;
    width: 40px;
    height: 40px;
    @media(max-width:1400px){
        width: 30px;
        height: 30px;
    }
    @media(max-width:970px){
        width: 28px;
        height: 28px;
    }
}
#apple{
    background: url('../../../../assets/landing/images/aStore.svg') no-repeat center center / cover;
    width: 31px;
    height: 38px;
    @media(max-width:1400px){
        width: 24px;
        height: 30px;
    }
    @media(max-width:970px){
        width: 22px;
        height: 27px;
    }
}