.RevenuePRM {
  display: flex;
  align-items: center;
  margin-top: 20px;
}
@media (max-width: 1300px) {
  .RevenuePRM {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.RevenuePRM-block {
  background: var(--background-color-first);
  display: flex;
  padding-top: 20px;
  padding-bottom: 20px;
  width: 100%;
  border-radius: 20px;
  box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
  margin-left: 20px;
}
.RevenuePRM-block:first-child {
  margin-left: 0px;
}
@media (max-width: 1500px) {
  .RevenuePRM-block {
    max-width: 382px;
  }
}
@media (max-width: 1500px) {
  .RevenuePRM-block {
    padding-top: 15px;
    padding-bottom: 15px;
  }
}
@media (max-width: 1300px) {
  .RevenuePRM-block {
    margin-top: 20px;
  }
  .RevenuePRM-block:nth-child(3) {
    margin-left: 0px;
  }
}
@media (max-width: 1115px) {
  .RevenuePRM-block {
    max-width: 300px;
  }
}
@media (max-width: 957px) {
  .RevenuePRM-block {
    max-width: 400px;
  }
}
@media (max-width: 862px) {
  .RevenuePRM-block {
    max-width: 350px;
  }
}
@media (max-width: 765px) {
  .RevenuePRM-block {
    max-width: 300px;
  }
}
@media (max-width: 660px) {
  .RevenuePRM-block {
    max-width: 270px;
  }
}
@media (max-width: 600px) {
  .RevenuePRM-block {
    max-width: 500px;
    margin-left: 0px;
  }
}
@media (max-width: 530px) {
  .RevenuePRM-block {
    max-width: 400px;
    margin-left: 0px;
  }
}

.RevenuePRM-text {
  margin-left: 18px;
}
.RevenuePRM-text h6 {
  color: var(--nav-list-disable);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  margin-bottom: 0px;
  line-height: 24px;
  letter-spacing: -0.28px;
}
@media (max-width: 1400px) {
  .RevenuePRM-text h6 {
    font-size: 12px;
  }
}
.RevenuePRM-text h5 {
  color: var(--blue-first);
  font-size: 24px;
  margin-top: 0px;
  font-style: normal;
  font-weight: 700;
  margin-bottom: 0px;
  line-height: 32px;
  letter-spacing: -0.48px;
}
@media (max-width: 1400px) {
  .RevenuePRM-text h5 {
    font-size: 20px;
  }
}
@media (max-width: 1300px) {
  .RevenuePRM-text h5 {
    font-size: 17px;
  }
}
@media (max-width: 825px) {
  .RevenuePRM-text h5 {
    font-size: 20px;
  }
}

.RevenuePRM-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 56px;
  background: var(--background-color-three);
  height: 56px;
  margin-left: 17px;
}
.RevenuePRM-icon__img {
  filter: var(--revenua-icon);
}

#RevenuePRM-contacts {
  background: url("../../../../assets/account/images/revenuaContacts.svg") no-repeat center center/cover;
  width: 21px;
  height: 21px;
}

#RevenuePRM-profit {
  background: url("../../../../assets/account/images/revenuaDollar.svg") no-repeat center center/cover;
  width: 28px;
  height: 28px;
}

#RevenuePRM-tokens {
  background: url("../../../../assets/account/images/revenuaTokens.svg") no-repeat center center/cover;
  width: 28px;
  height: 28px;
}

#RevenuePRM-rate {
  background: url("../../../../assets/account/images/revenuaRate.svg") no-repeat center center/cover;
  width: 28px;
  height: 28px;
}/*# sourceMappingURL=RevenuePRM.css.map */