.BurgerMenu_active{
    display: none;
    position: absolute;
    top: 80px;
    z-index: 99;
    right: -15px;
    transition: all 0.4s;

    width: 65%;
    height: auto;
    
    border-radius: 26px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    background: rgb(51, 51, 51);

    @media(max-width:1160px) {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    @media(max-width:500px) {
        width: 70%;
    }
    @media(max-width:400px) {
        width: 100%;
    }

}
.BurgerMenu_deactive{
    display: none;
    
    @media(max-width:1160px) {
        transition: all 0.3s;
        position: absolute;
        top: 80px;
        right: -60%;
        transition: all 0.4s;
        width: 50%;
        height: auto;
        border-radius: 26px;
        border: 1px solid rgba(255, 255, 255, 0.12);
        background: rgba(255, 255, 255, 0.11);
    }
    @media(max-width:500px) {
       width: 70%;
       right: -90%;
    }
    @media(max-width:400px) {
        width: 100%;
        right: -105%;
    }
}
.BurgerMenu__nav-list{
    width: 100%;
    display: flex;
    justify-content: space-around;
    list-style: none;
    padding-top: 25px;
    @media(max-width:700px) {
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    &__row-1{
        
    }
    &__row-2{

    }
    
}
.BurgerMenu__nav-list__item{
    border-bottom: 1px solid rgb(58, 58, 63);
    padding-top: 15px;
    padding-bottom: 15px;
    width: 100%;
    color: #FFF;
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
        transition: all 0.2s;
        line-height: normal;
    cursor: pointer;
    @media(max-width:500px) {
        padding-top: 12px;
        padding-bottom: 12px;
    }
    &:hover{
        color: #ffffff7c;
        transition: all 0.2s;
    }
}
.BurgerMenu__nav-button-cont{
    margin-top: 25px;
    width: 80%;
    display: flex;
    justify-content: center;
    padding-bottom: 50px;
    &:hover{
        .BurgerMenu__nav-button{
            background: #FFF;
            transition: all 0.4s;
            h5{
                color:#000;
                transition: all 0.4s;
            }
        }
        
    }
}
.BurgerMenu__nav-button{
    width: 100%;
    height: 53px;
    cursor: pointer;
    border-radius: 30px;
    background: #000;
    transition: all 0.4s;
    border: 1px solid #FFF;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover{
        background: #FFF;
        transition: all 0.4s;
    }


    h5{
        color: #FFF;
        text-align: center;
        font-family: 'Open Sans';
        font-size: 16 px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
    
}
