.PartnerManagament{
    margin-top: 100px;
    
}
.PartnerManagament__title{
    font-size: 87px;
    font-style: normal;
    font-weight: 800;
    line-height: 70px;
    max-width: 867px;
    margin-left: 20px;
    @media(max-width: 1800px) {
        margin-left: 0px;
        max-width: 600px;
    }
    @media(max-width: 1750px) {
        font-size: 69px;
    }
    @media(max-width: 1250px) {
        font-size: 65px;
    }
    @media(max-width: 1100px) {
        font-size: 60px;
        line-height: 60px;
    }
    @media(max-width: 960px) {
        text-align: center;
    }
    @media(max-width:600px){
        line-height: 50px;
        font-size: 40px;
    }
    @media(max-width:500px) {
        font-size: 30px;
        line-height: 30px;
    }
}
.PartnerManagament__text{
    color: #FFF;
    font-family: 'Open Sans';
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    max-width: 861px;
    margin-left: 20px;
    width: 100%;
    margin-top: 37px;
    @media(max-width: 1800px) {
        margin-left: 0px;
    }
    @media(max-width: 1750px) {
        font-size: 26px;
        max-width: 600px;
    }
    @media(max-width: 1250px) {
        font-size: 24px;
        max-width: 500px;
    }
    @media(max-width: 1100px) {
        display: none;
        text-align: center;
        margin-top: 450px;
        max-width: 900px;
    }
    @media(max-width: 960px) {
        margin-top: 650px;
    }
    @media(max-width:600px){
        font-size: 20px;
    }
}
.row-1{
    display: flex;
    justify-content: space-between;
    @media(max-width: 960px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

}
.row-2{
    display: flex;
    justify-content: space-between;
    @media(max-width: 1100px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}
.PartnerManagament__title-2{
    font-size: 87px;
    font-style: normal;
    font-weight: 800;
    line-height: 70px;
    max-width: 650px;
    margin-top: 400px;
    position: relative;
    @media(max-width: 1750px) {
        font-size: 69px;
        margin-top: 400px;
        max-width: 600px;
    }
    @media(max-width: 1600px) {
        font-size: 60px;
        margin-top: 400px;
    }
    @media(max-width: 1420px) {
        text-align: right;
        margin-top: 300px;
    }
    
    @media(max-width: 1100px) {
        margin-top: 100px;
        text-align: center;
        max-width: 700px;
    }
    @media(max-width:600px){
        line-height: 50px;
        font-size: 40px;
        text-align: center;
    }
    
    &::before{
        content: '';
        width: 562px;
        z-index: -1;

        height: 562px;
        transform: rotate(15.541deg);
        background: linear-gradient(180deg, #EAC7FF 0%, #6373FF 70.14%);;
        filter: blur(300px);
        transform: rotate(15.541deg);
        position: absolute;
        right: 100px;
        bottom: -50%;
        @media(max-width: 1500px) {
            right: 100px;
            width: 400px;
            height: 400px;
        }
        @media(max-width:776px) {
            width: 0px;
            height: 0px;
        }
    }
}
#second-txt{
    display: none;
    @media(max-width: 1100px) {
        display: block;
    }
}