.Navbar{
    max-width: 1628px;
    border-radius: 50px;
    margin-top: 60px;
    z-index: 99;
    background: rgba(26, 26, 26, 0.60);
    backdrop-filter: blur(10px);
    margin: 0px auto;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 98;
    @media(max-width:1760px) {
        height: 70px;
    }
    
    &__wrapper{
        max-width: 1650px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    padding-right: 39px;
    @media(max-width:500px) {
        padding-right: 10px;;
    }
    }
}
.nav-list{
    display: flex;
    align-items: center;
    list-style: none;
    margin-left: 94px;
    @media(max-width:1500px) {
        margin-left: 30px;
    }
    @media(max-width:1160px) {
        margin-left: 20px;
    }
    @media(max-width:1160px) {
        display: none;
    }
    &__item{
        color: #FFF;
        text-align: center;
        font-family: 'Inter';
        transition: all 0.3s;
        font-size: 24px;
        font-style: normal;
        font-weight: 400;
        white-space: nowrap;
        line-height: normal;
        margin-left: 44px;
        a{
            text-decoration: none;
            color: #FFF;
        }
        @media(max-width:1800px) {
            margin-left: 18px;
            height: 35px;
            font-size: 18px;
            margin-top: 10px;
        }
        @media(max-width:1400px) {
            font-size: 16px;
        }
        @media(max-width:1280px) {
            font-size: 16px;
            margin-left: 10px;
        }
        &:hover{
            color:#ffffff9d;
            transition: all 0.3s;
        }
        &:first-child{
            margin-left: 0;
        }
        cursor: pointer;
    }
}
.logo-nav{
    max-width: 270px;
    padding-left: 39px;
    width: 100%;
    @media(max-width:500px) {
        max-width: 240px;
    }
    @media(max-width:400px) {
        max-width: 220px;
    }
    img{
        width: 100%;
        height: 22px;
        object-fit: cover;
        object-position: center center;
        
        @media(max-width:1800px) {
            height: 21px;
        }
        
        @media(max-width:1500px) {
            height: 21px;
        }
        @media(max-width:1300px) {
            height: 19px;
        }
        @media(max-width:1250px) {
            height: 17px;
        }
        @media(max-width:1160px) {
            height: 24px;
        }
        @media(max-width:500px) {
            height: 19px;
        }
        @media(max-width:400px) {
            height: 18px;
        }
    }
}
.nav-button{
    max-width: 260px;
    width: 100%;
    height: 53px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 94px;
    cursor: pointer;
    border-radius: 30px;
    background: #000;
    transition: all 0.4s;
    border: 1px solid #FFF;
    margin-left: 35px;
    @media(max-width:1800px) {
        margin-left: 30px;
        height: 35px;
    }
    @media(max-width:1200px) {
        margin-left: 35px;
        white-space: nowrap;
        padding-left: 10px;
        padding-right: 10px;
        margin-left: 20px;
    }
    @media(max-width:1160px) {
        display: none;
    }
    &__text{
        color: #FFF;
        text-align: center;
        font-family: 'Open Sans';
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        transition: all 0.4s;
        white-space: nowrap;
        line-height: normal;
        @media(max-width:1620px) {
            font-size: 18px;
        }
    }
    &:hover{
        background: #FFF;
        transition: all 0.4s;
        .nav-button__text{
            color:#000;
            transition: all 0.4s;
        }
    }
}

.burger-show_button{
    display: none;
    background: url('../../../../assets/landing/images/burger-menu.png') no-repeat center center / cover;
    width: 38px;
    height: 38px;
    @media(max-width:1160px) {
        display: block;
        cursor: pointer;
    }
    @media(max-width:600px) {
        width: 28px;
        height: 28px;
        margin-left: 20px;
    }
}