.EditProfile{
    padding-left: 31px;
    padding-top: 20px;
    padding-right: 25px;
    padding-bottom: 41px;
    width: 100%;
    margin-top: 20px;
    background-color: var(--background-color-four);
    border-radius: 20px;
    box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
    @media(max-width: 1400px) {
        max-width: 100%;
    }
}
.EditProfile__body{
    display: flex;
    justify-content: space-between;
    @media(max-width: 1350px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}
.blueButton-cont{
    margin-top: 75px;
    max-width: 200px;
    width: 100%;
    @media(max-width: 1350px) {
        margin: 0px auto;
        margin-top: 75px;
    }
}