.ColorPicker{
    margin-left: 42px;
    @media(max-width: 1860px){
        margin-left: 0px;
        margin-top: 40px;
    }
    &__picker{
        width: 400px;
        @media(max-width: 500px){
            width: 250px ;
        }
        position: relative;
    }
}
.sketch-picker {
    max-width: 340px !important;
    width: 100% !important;
    height: 550px !important;
    background-color: var(--background-color-secondary) !important;
    border: 3px solid var(--blue-button-border) !important;
    @media(max-width: 500px){
        height: 350px !important;
    }
}

.button_confirm{
    border-radius: 4px;
    color:var(--color-picker-color);
    white-space: nowrap;
    font-family: 'Open Sans';
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    border-radius: 70px;
    line-height: 32px;
    letter-spacing: -0.3px;
    background-color: var(--blue-first);
    padding: 6px 20px;
    text-align: center;
    cursor: pointer;
    width: 106px;
    transition: all 0.4s;
    position: absolute;
    bottom: 15px;
    right: 55px;

    text-decoration: none;
    text-align: center;
    border: 1px solid var(--blue-button-border);
    &:hover{
        background: var(--blue-button-hover);
        transition: all 0.4s;
    }
    @media(max-width: 500px) {
        width: 80px;
        padding: 0px 10px;
        font-size: 12px;
        right: 0px;
        bottom: 12px;
        line-height: 24px;
    }
}