.FinancesPotencial {
  padding-left: 31px;
  padding-top: 29px;
  padding-right: 32px;
  padding-bottom: 74px;
  width: 100%;
  margin-top: 20px;
  background-color: var(--background-color-four);
  border-radius: 20px;
  box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
