.FAQ {
  position: relative;
}
.FAQ_marque-item {
  margin-left: 95px;
}
.FAQ::before {
  content: "";
  background: linear-gradient(180deg, #EAC7FF 0%, #6373FF 70.14%);
  filter: blur(300px);
  width: 352.61px;
  height: 352.61px;
  position: absolute;
  transform: rotate(15.541deg);
  left: 0px;
  top: 180px;
  z-index: -1;
}
@media (max-width: 450px) {
  .FAQ::before {
    width: 0px;
    height: 0px;
  }
}
.FAQ::after {
  content: "";
  background: linear-gradient(180deg, #EAC7FF 0%, #6373FF 70.14%);
  filter: blur(300px);
  width: 352.61px;
  height: 352.61px;
  position: absolute;
  transform: rotate(15.541deg);
  right: 200px;
  bottom: 180px;
  z-index: -1;
}
@media (max-width: 450px) {
  .FAQ::after {
    width: 150px;
    height: 150px;
  }
}/*# sourceMappingURL=FAQ.css.map */