.container_1{
    position: relative;
    max-width: 850px;
    width: 100%;
    padding-left: 59px;
    @media(max-width: 1200px){
        padding-left:0px;
    }
    @media(max-width: 1000px){
        max-width: 950px;
        padding: 15px;
        margin: 0px auto;
    }
}
.container_2{
    position: relative;
    max-width: 967px;
    width: 100%;
    padding-bottom: 26px;
    @media(max-width: 1350px){
        max-width: 500px;
    }
    @media(max-width: 1000px){
        max-width: 1000px;
        width: 100%;
        margin-top: 50px;
    }
}
.Registration {
    display : flex;
    background-color: var(--background-color-first);
    justify-content: space-between;
    @media(max-width: 1000px){
        flex-direction: column;
    }
}
.PS {
    color: #A3AED0;
    font-family: 'DM Sans';
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: -0.28px;
    margin: 0px auto;
    margin-top: 142px;
    text-align: center;
    margin-right: 70px;
    @media(max-width: 1200px){
        margin-top: 20px;
    }
    @media(max-width:390px){
        margin-right: 0px;
    }
}