.MyContacts{
    padding-left: 31px;
    padding-top: 29px;
    padding-right: 32px;
    padding-bottom: 20px;
    width: 100%;
    margin-top: 20px;
    background-color: var(--background-color-four);
    border-radius: 20px;
    box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
    @media(max-width: 1400px) {
        max-width: 100%;
        margin-left: 0px;
    }
    &__head{
        display: flex;
        justify-content: space-between;
        @media(max-width: 700px) {
            flex-direction: column;
            justify-content: none;
        }
        &__block{
            display: flex;
            align-items: center;
            margin-top: 10px;
            &__buttons{
                display: flex;
                align-items: center;
                width: 100%;
                justify-content: space-between;
                @media(max-width: 450px) {
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                }
            }
        }
    }
}
.MyContacts-icon{
    margin-left: 33px;
    @media(max-width: 1000px){
        margin-left: 10px;
    }
}