.Finances{
    padding-left: 31px;
    padding-top: 29px;
    padding-right: 32px;
    padding-bottom: 74px;
    width: 100%;
    margin-top: 20px;
    background-color: var(--background-color-four);
    border-radius: 20px;
    box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
    @media(max-width: 1400px) {
        max-width: 100%;
        margin-left: 0px;
    }
}
.Finances__body__profits{
    display: flex;
    align-items: center;
    margin-top: 36px;
    @media(max-width: 1400px) {
        justify-content: space-around;
    }
    @media(max-width: 430px) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
}
.Finances__body__buttons{
    display: flex;
    align-items: center;
    margin-top: 33px;
    @media(max-width: 1400px) {
        justify-content: center;
    }
    @media(max-width: 430px) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
.Finances-icon{
    background: url('../../../../assets/account/images/revenuaTokens.svg') no-repeat center center / cover;
}
.blue-button-first-cont{
}
.blue-buttons-secondary-cont{
    display: flex;
    align-items: center;
}