.StrategyEditor{
    padding-left: 31px;
    padding-top: 20px;
    
    padding-right: 25px;
    padding-bottom: 400px;
    margin-top: 20px;
    width: 100%;
    background-color: var(--background-color-four);
    border-radius: 20px;
    box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
    @media(max-width: 1860px) {
        margin-left: 0px;
        max-width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__body{
        display: flex;
        @media(max-width: 1860px) {
            flex-direction: column;
        }
    }
}
.statusAndChange{
    display: flex;
    
    justify-content: space-between;
    flex-direction: column;
    width: 320px;
    margin-left: 26px;
    @media(max-width: 1860px) {
        width: 800px;
        margin-top: 60px;
        margin-left: 0px;
        min-height: 150px;
        align-items: center;
    }
}
.StrategyEditor__block-1{
    
    display: flex;
    @media(max-width: 1860px) {
        flex-direction: column;
        align-items: center;
    }
}
.StrategyEditor__block-2{
    display: flex;
    height: 700px;
    @media(max-width: 1860px) {
        flex-direction: column;
        align-items: center;
        height: auto;
    }
}
.Strategy__Editor__dropdown{
    @media(max-width: 1860px){
        display: flex;
        justify-content: center;
    }
}
.triple-line-icon{
    background: url('../../../../assets/account/images/triple-line.svg') no-repeat center center / cover;
    width: 18px;
    height: 18px;
    position: absolute;
    top: 14px;
    right: 45px;
    cursor: grab;
    &:active{
        cursor: grabbing;
    }
}
.triple-line-icon_dark{
    background: url('../../../../assets/account/images/triple-line-dark-theme.svg') no-repeat center center / cover;
    width: 18px;
    height: 18px;
    position: absolute;
    top: 14px;
    right: 45px;
    cursor: grab;
    &:active{
        cursor: grabbing;
    }
}
.delete-status-icon{
    background: url('../../../../assets/account/images/delete-status-icon.svg') no-repeat center center / cover;
    width: 16px;
    height: 18px;
    position: absolute;
    top: 14px;
    right: 14px;
}
.StrategyEditor__dropdown-item_active{
    background: var(--dropdown-color-active);
    &::after{
        content: none;
    }
}
.StrategyEditor__dropdown-item_active {
    transition: all 0.3s cubic-bezier(0.4, 0.0, 0.2, 1);
}

.StrategyEditor__dropdown-item_active.dragging {
    opacity: .2;
}
.drop{
    border: 2px dashed var(--drag-line);
}
.StrategyEditor__addStatus{
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: var(--text-color-three);
    color: #FFF;
    &:hover{
        background-color: var(--text-color-three-hover);
    }
}
.StatusEditor-but{
    display: flex;
    align-items: center;
}
.StatusEditor__first-block{
    display: flex;
    align-items: center;
    @media(max-width: 1670px){
        justify-content: space-between;
        width: 100%;
    }
}
.StrategyEditor_prev{
    margin-left: 100px;
    @media(max-width: 400px){
        margin-left: 20px;
    }
}