.Market {
  margin-top: 51px;
}
.Market__buy-bar {
  margin-top: 47px;
}
.Market__buy-bar:first-child {
  margin-top: 0px;
}
.Market__buy-bar__head__title {
  color: var(--blue-first);
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: -0.48px;
}
.Market__buy-bar__body {
  overflow-x: auto;
  margin-top: 20px;
  padding-bottom: 40px;
}
.Market__buy-bar__body::-webkit-scrollbar {
  height: 12px;
  border-radius: 10px;
}
.Market__buy-bar__body::-webkit-scrollbar-thumb {
  background: var(--scrollbar-color-line);
  border-radius: 10px;
}
.Market__buy-bar__body::-webkit-scrollbar-track {
  background: var(--background-color-four);
}

.first-bar-head {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 488px) {
  .first-bar-head {
    flex-direction: column;
    align-items: center;
  }
}

.Market-buttons {
  display: flex;
  align-items: center;
}
@media (max-width: 488px) {
  .Market-buttons {
    margin-top: 15px;
  }
}
@media (max-width: 370px) {
  .Market-buttons {
    flex-direction: column;
    align-items: center;
  }
}
.Market-buttons__button {
  color: var(--text-color-three);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.28px;
  background: transparent;
  transition: all 0.3s;
  cursor: pointer;
  border-radius: 70px;
  padding: 4px 14px;
}
@media (max-width: 488px) {
  .Market-buttons__button {
    margin-left: 10px;
  }
}
@media (max-width: 370px) {
  .Market-buttons__button {
    margin-left: 0px;
  }
}
.Market-buttons__button:hover {
  background-color: var(--background-color-four);
  transition: all 0.3s;
}
.Market-buttons__button:first-child {
  background-color: var(--background-color-four);
  transition: all 0.3s;
}
.Market-buttons__button:first-child:hover {
  background-color: var(--text-color-three);
  color: var(--background-color-four);
  transition: all 0.3s;
}/*# sourceMappingURL=Market.css.map */