.MyTeam{
    padding-left: 31px;
    padding-top: 29px;
    padding-right: 32px;
    padding-bottom: 20px;
    width: 100%;
    margin-top: 20px;
    background-color: var(--background-color-four);
    border-radius: 20px;
    box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
    @media(max-width: 1400px) {
        max-width: 100%;
        margin-left: 0px;
    }
}
.MyTeam__body{
    margin-top: 23px;
    overflow-x: auto;
    padding-bottom: 10px;
    &::-webkit-scrollbar {
      height: 12px;
      border-radius: 10px;
  }
  &::-webkit-scrollbar-thumb {
      background: var(--scrollbar-color-line);
      border-radius: 10px;
  }
  &::-webkit-scrollbar-track {
      background: var(--background-color-four);
  }
    &__table{
        width: 100%;
        @media(max-width: 1800px){
            min-width: 100%;
        }
        &__head{
            display: flex;
            align-items: center;
            border-bottom: 1px solid var(--line-one);
            width: 100vw;
            padding-bottom: 8px;
            &__item{
                display: flex;
                align-items: center;
                width: 100%;
                min-width: 200px;
                @media(max-width: 958px) {
                    min-width: 200px;
                }
                &__text{
                    white-space: nowrap;
                    color: var(--nav-list-disable);
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 500;
                    line-height: 24px; 
                    letter-spacing: -0.28px;
                    
                }
                &__icon{
                    background: url('../../../../assets/account/images/arrow-down-list.svg') no-repeat center center / cover;
                    width: 24px;
                    height: 24px;
                    margin-left: 7px;
                }
            }
        }
    }
}
.__select {
    position: relative;
    width: 230px;
    height: 40px;
    margin: 0 auto;
  
    &[data-state="active"] {
      .__select__title {
        &::before {
          transform: translate(-3px, -50%) rotate(-45deg);
        }
  
        &::after {
          transform: translate(3px, -50%) rotate(45deg);
        }
      }
      
      .__select__content {
        opacity: 1;
      }
      
      .__select__label + .__select__input + .__select__label {
        max-height: 40px;
        border-top-width: 1px;
      }
    }
  }
  .__select__title {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 8px 16px;
  
    border-radius: 5px;
    border: solid 1px #c7ccd1;
    
    cursor: pointer;
  
    &::before,
    &::after {
      content: "";
  
      position: absolute;
      top: 50%;
      right: 16px;
  
      display: block;
      width: 10px;
      height: 2px;
  
      transition: all 0.3s ease-out;
  
      background-color: #333333;
  
      transform: translate(-3px, -50%) rotate(45deg);
    }
    &::after {
      transform: translate(3px, -50%) rotate(-45deg);
    }
  
    &:hover {
      border-color: #D8093A;
  
      &::before,
      &::after {
        background-color: #D8093A;
      }
    }
  }
  .reset {
    display: flex;
    width: 230px;
    padding: 8px 16px;
    margin: 0 auto;
    margin-bottom: 10px;
    
    border: solid 1px #c7ccd1;
    border-radius: 5px;
    
    transition: all 0.2s ease-out;
    
    cursor: pointer;
    
    font-weight: bold;
    
    background-color: #ffffff;
    color: #333333;
    
    &:hover {
      background-color: #D8093A;
      color: #ffffff;
    }
  }
  .__select__content {
    position: absolute;
    top: 40px;
    left: 3px;
  
    display: flex;
    flex-direction: column;
    width: calc(100% - 6px);
  
    background-color: #ffffff;
  
    border: 1px solid #c7ccd1;
    border-top: none;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    
    transition: all 0.3s ease-out;
  
    opacity: 0;
    z-index: 8;
  }
  .__select__input {
    display: none;
  
    &:checked + label {
      background-color: #dedede;
    }
    &:disabled + label {
      opacity: 0.6;
      pointer-events: none;
    }
  }
  .__select__label {
    display: flex;
    align-items: center;
    width: 100%;
    height: 40px;
    max-height: 0;
    padding: 0 16px;
    
    transition: all 0.2s ease-out;
  
    cursor: pointer;
    
    overflow: hidden;
  
    & + input + & {
      border-top: 0 solid #C7CCD160;
    }
  
    &:hover {
      background-color: #D8093A !important;
  
      color: #ffffff;
    }
  }
  .MyTeam__bottom{
    display: flex;
    justify-content: space-between;
    margin-top: 25px;
    @media(max-width:500px) {
      flex-direction: column;
      align-items: center;
      justify-content: center;
   }
  }
  .MyTeam__bottom__pagination{
    display: flex;
    align-items: center;
    @media(max-width:500px) {
      margin-top: 20px;
   }
    &_arrow-left{
      background: url('../../../../assets/account/images/arrow-down-list.svg') no-repeat center center / cover;
      transform: rotate(90deg);
      width: 15px;
      height: 15px;
      cursor: pointer;
      @media(max-width:500px) {
        width: 35px;
        height: 35px;
     }
    }
    &_arrow-right{
      background: url('../../../../assets/account/images/arrow-down-list.svg') no-repeat center center / cover;
      width: 15px;
      height: 15px;
      transform: rotate(-90deg);
      cursor: pointer;
      @media(max-width:500px) {
        width: 35px;
        height: 35px;
     }
    }
    &__body{
      display: flex;
      align-items: center;
      &__item{
        cursor: pointer;
        font-size: 14px;
        font-weight: 400;
        color: var(--nav-list-disable);
        margin-left: 10px;
        @media(max-width:500px) {
          font-size: 24px;
       }
      }
    }
  }
  .MyTeam__bottom__pagination__body__item_active{
    cursor: pointer;
      font-size: 14px;
      font-weight: 500;
      color: var(--text-color-secondary);
      margin-left: 10px;
      @media(max-width:500px) {
        font-size: 24px;
     }
  }