.Profit{
    h1{
        color: var(--blue-secondary);
        @media(max-width: 430px){
            text-align: center;
        }
        font-size: 48px;
        font-style: normal;
        font-weight: 700;
        line-height: 100%; 
        letter-spacing: -0.96px;
        white-space: nowrap;
        @media(max-width: 500px) {
            font-size: 38px;
        }
    }
    h5{
        color: var(--nav-list-disable);
        margin-top: 2px;
        @media(max-width: 430px){
            text-align: center;
        }
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px; 
        letter-spacing: -0.28px;
        @media(max-width: 500px) {
            font-size: 12px;
        }
    }
}