.LastContact {
  padding-left: 35px;
  padding-top: 29px;
  padding-right: 32px;
  padding-bottom: 74px;
  max-width: 820px;
  width: 100%;
  margin-top: 20px;
  background-color: var(--background-color-four);
  border-radius: 20px;
  box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
}
@media (max-width: 1400px) {
  .LastContact {
    max-width: 100%;
  }
}

.LastContact-icon {
  background: url("../../../../assets/account/images/revenuaContacts.svg") no-repeat center center/cover;
}

.LastContact__body {
  display: flex;
  margin-top: 11px;
  justify-content: space-between;
}
@media (max-width: 700px) {
  .LastContact__body {
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top: 27px;
  }
}
.LastContact__body__contactAndButtons__contact {
  display: flex;
  align-items: center;
}
.LastContact__body__contactAndButtons__contact__avatar {
  width: 100px;
  border-radius: 100%;
}
.LastContact__body__contactAndButtons__contact__avatar img {
  width: 100%;
  border-radius: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: center center;
     object-position: center center;
}
.LastContact__body__contactAndButtons__contact__text {
  margin-left: 12px;
}
.LastContact__body__contactAndButtons__contact__text h5 {
  color: var(--nav-list-disable);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.28px;
  margin-top: 0px;
  margin-bottom: 0px;
}
.LastContact__body__contactAndButtons__buttons {
  display: flex;
  align-items: center;
  margin-top: 33px;
}
@media (max-width: 700px) {
  .LastContact__body__contactAndButtons__buttons {
    display: none;
  }
}
.LastContact__body__contactAndButtons__buttons__button {
  padding: 6px 20px;
  background: var(--ref-button);
  border-radius: 70px;
  margin-left: 14px;
  min-width: 106px;
  cursor: pointer;
  color: #FFF;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.28px;
}
.LastContact__body__contactAndButtons__buttons__button:first-child {
  margin-left: 0px;
}

.speedTest {
  margin-right: 20px;
  width: 276px;
  height: 196px;
  background: url("../../../../assets/account/images/speedometer.png") no-repeat center center/cover;
}
@media (max-width: 1580px) {
  .speedTest {
    width: 200px;
    height: 130px;
  }
}
@media (max-width: 700px) {
  .speedTest {
    margin-top: 11px;
    margin-right: 0px;
  }
}

.LastContact__body__contactAndButtons__buttons_disable {
  display: none;
}
@media (max-width: 700px) {
  .LastContact__body__contactAndButtons__buttons_disable {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 24px;
  }
}
@media (max-width: 460px) {
  .LastContact__body__contactAndButtons__buttons_disable {
    flex-direction: column;
    align-items: center;
  }
}

#img-LastContact-cont {
  position: relative;
}
#img-LastContact-cont::before {
  content: "";
  background: url("../../../../assets/account/images/avatar-psevdo-image.png") no-repeat center center/cover;
  width: 26px;
  height: 26px;
  position: absolute;
  right: 0px;
  bottom: 0px;
  cursor: pointer;
}/*# sourceMappingURL=LastContact.css.map */