.Market{
    margin-top: 51px;
    &__buy-bar{
        margin-top: 47px;
        &:first-child{
            margin-top: 0px;
        }
        &__head{
            &__title{
                color: var(--blue-first);
                font-size: 24px;
                font-style: normal;
                font-weight: 700;
                line-height: 32px;
                letter-spacing: -0.48px;
            }
        }
        &__body{
            overflow-x: auto;
            margin-top: 20px;
            padding-bottom: 40px;
            &::-webkit-scrollbar {
                height: 12px;
                border-radius: 10px;
            }
            &::-webkit-scrollbar-thumb {
                background: var(--scrollbar-color-line);
                border-radius: 10px;
            }
            &::-webkit-scrollbar-track {
                background: var(--background-color-four);
            }
        }
    }
}
.first-bar-head{
    display: flex;
    justify-content: space-between;
    @media(max-width: 488px){
        flex-direction: column;
        align-items: center;
    }
}
.Market-buttons{
    display: flex;
    align-items: center;
    @media(max-width: 488px){
        margin-top: 15px;
    }
    @media(max-width: 370px){
        flex-direction: column;
        align-items: center;
    }
    &__button{
        @media(max-width: 488px){
            margin-left: 10px;
        }
        @media(max-width: 370px){
            margin-left: 0px;
        }
        color: var(--text-color-three);
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        letter-spacing: -0.28px;
        background: transparent;
        transition: all 0.3s;
        cursor: pointer;
        &:hover{
            background-color: var(--background-color-four);
            transition: all 0.3s;
        }
        &:first-child{
            background-color: var(--background-color-four);
            transition: all 0.3s;
            &:hover{
                background-color: var(--text-color-three);
                color: var(--background-color-four);
                transition: all 0.3s;
            }
        }
        border-radius: 70px;
        padding: 4px 14px;
    }
}