.SideNavMenu {
  min-width: 290px;
  max-width: 290px;
  background-color: var(--background-color-four);
}
@media (max-width: 957px) {
  .SideNavMenu {
    display: none;
  }
}
.SideNavMenu__logo {
  background: url("../../../../assets/account/images/prm-logo-2.png") no-repeat center center/cover;
  max-width: 210px;
  width: 100%;
  height: 24px;
  margin-top: 56px;
  margin-left: 30px;
}
.SideNavMenu__logo_dark-theme {
  background: url("../../../../assets/account/images/prm4-logo-darkTheme.png") no-repeat center center/cover;
  max-width: 210px;
  width: 100%;
  height: 24px;
  margin-top: 56px;
  margin-left: 30px;
}
.SideNavMenu__logo[data-theme=dark] {
  background: url("../../../../assets/account/images/prm4-logo-darkTheme.png") no-repeat center center/cover;
}
.SideNavMenu__nav-list {
  margin-top: 89px;
  padding-left: 30px;
}

.SideNavMenu__nav-list__item_active {
  display: flex;
  align-items: center;
  transition: all 0.2s;
  margin-top: 24px;
  cursor: pointer;
  border-right: 4px solid var(--active-side-navbar-color);
  margin-bottom: 0px;
}
.SideNavMenu__nav-list__item_active:first-child {
  margin-top: 0px;
}
.SideNavMenu__nav-list__item_active .SideNavMenu-icon {
  width: 20px;
  height: 20px;
  filter: var(--side-nav-bar-menu-icon-active);
}
.SideNavMenu__nav-list__item_active .SideNavMenu__nav-item_link, .SideNavMenu__nav-list__item_active h3 {
  color: var(--active-side-navbar-color);
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: -0.32px;
  margin-left: 12px;
  margin-bottom: 0px;
}

.SideNavMenu__nav-list__item {
  display: flex;
  align-items: center;
  transition: all 0.2s;
  margin-top: 24px;
  border-right: 4px solid transparent;
  cursor: pointer;
}
.SideNavMenu__nav-list__item:hover {
  transition: all 0.2s;
  border-right: 4px solid var(--nav-list-hover);
  padding-right: 30px;
}
.SideNavMenu__nav-list__item:hover .SideNavMenu__nav-item_link, .SideNavMenu__nav-list__item:hover h3 {
  color: var(--nav-list-hover);
}
.SideNavMenu__nav-list__item .SideNavMenu-icon {
  width: 20px;
  height: 20px;
  filter: var(--side-nav-bar-menu-icon);
}
.SideNavMenu__nav-list__item .SideNavMenu__nav-item_link, .SideNavMenu__nav-list__item h3 {
  color: var(--nav-list-disable);
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: -0.32px;
  margin-left: 12px;
  margin-bottom: 0px;
}

.SideNavMenu__blob-container {
  margin-top: 100px;
  width: 280px;
  margin-left: 0px;
}
@media (max-width: 1950px) {
  .SideNavMenu__blob-container {
    margin-left: 20px;
  }
}

#nav-main {
  background: url("../../../../assets/account/images/main-icon.svg") no-repeat center center/cover;
}

#nav-finance {
  background: url("../../../../assets/account/images/finance-icon.svg") no-repeat center center/cover;
}

#nav-team {
  background: url("../../../../assets/account/images/team-icon.svg") no-repeat center center/cover;
}

#nav-contacts {
  background: url("../../../../assets/account/images/contacts-icon.svg") no-repeat center center/cover;
}

#nav-marketplace {
  background: url("../../../../assets/account/images/marketplace-icon.svg") no-repeat center center/cover;
}

#nav-token {
  background: url("../../../../assets/account/images/token.svg") no-repeat center center/cover;
}

#nav-profile {
  background: url("../../../../assets/account/images/profile.svg") no-repeat center center/cover;
}

#nav-sign-out {
  background: url("../../../../assets/account/images/sign-out-icon.svg") no-repeat center center/cover;
  width: 16px;
  height: 21px;
}

.SideNavMenu__nav-item_link {
  text-decoration: none;
  color: var(--nav-list-disable);
}/*# sourceMappingURL=SideNavMenu.css.map */