.Videos__reg-button-cont {
  width: 100%;
  display: flex;
  justify-content: center;
}

.Videos_marquee-item {
  margin-left: 5vw;
}

.Videos__players {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}
@media (max-width: 1180px) {
  .Videos__players {
    flex-direction: column;
    justify-content: center;
  }
}

.player-cont {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.player-cont h3 {
  color: #FFF;
  text-align: center;
  text-shadow: 1px 1px 20px #000;
  font-family: "Open Sans";
  font-size: 64px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-top: 34px;
}
@media (max-width: 1720px) {
  .player-cont h3 {
    margin-top: 40px;
  }
}
@media (max-width: 1600px) {
  .player-cont h3 {
    font-size: 35px;
  }
}
@media (max-width: 1180px) {
  .player-cont h3 {
    font-size: 25px;
  }
}
@media (max-width: 900px) {
  .player-cont h3 {
    font-size: 26px;
  }
}
@media (max-width: 720px) {
  .player-cont h3 {
    font-size: 22px;
    margin-top: 20px;
  }
}

.Videos__players__player {
  cursor: pointer;
  max-width: 785px;
  overflow: hidden;
  border-radius: 26px;
  border: 1px solid #454545;
}
@media (max-width: 1720px) {
  .Videos__players__player {
    margin-top: 20px;
  }
}
@media (max-width: 720px) {
  .Videos__players__player {
    margin-top: 20px;
  }
}
.Videos__players__player::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 482px;
  z-index: -1;
  height: 482px;
  transform: rotate(15.541deg);
  background: linear-gradient(180deg, rgba(234, 199, 255, 0.7803921569) 0%, rgba(99, 115, 255, 0.7254901961) 70.14%);
  filter: blur(300px);
}
@media (max-width: 1100px) {
  .Videos__players__player::after {
    width: 250px;
  }
}
@media (max-width: 450px) {
  .Videos__players__player::after {
    width: 0px;
    height: 0px;
    display: none;
  }
}
.Videos__players__player:first-child {
  margin-left: 0;
}

.videos__players__player_frame {
  width: 785px;
  height: 461px;
}
@media (max-width: 1830px) {
  .videos__players__player_frame {
    width: 650px;
    height: 360px;
  }
}
@media (max-width: 1540px) {
  .videos__players__player_frame {
    width: 550px;
    height: 320px;
  }
}
@media (max-width: 1320px) {
  .videos__players__player_frame {
    width: 480px;
    height: 280px;
  }
}
@media (max-width: 700px) {
  .videos__players__player_frame {
    width: 380px;
    height: 240px;
  }
}
@media (max-width: 500px) {
  .videos__players__player_frame {
    width: 300px;
    height: 170px;
  }
}
@media (max-width: 300px) {
  .videos__players__player_frame {
    width: 200px;
    height: 100px;
  }
}/*# sourceMappingURL=Videos.css.map */