.AccordeonItem {
  max-width: 1000px;
  width: 100%;
  cursor: pointer;
  margin-top: 25px;
}
.AccordeonItem:first-child {
  margin-top: 0px;
}
.AccordeonItem__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 24px;
  border: 2px solid rgba(255, 255, 255, 0.12);
  background: rgba(255, 255, 255, 0.0784313725);
  padding: 35px 45px;
}
@media (max-width: 1500px) {
  .AccordeonItem__header {
    font-size: 20px;
    padding: 25px 20px;
  }
}
@media (max-width: 1200px) {
  .AccordeonItem__header {
    font-size: 20px;
    padding: 15px 20px;
  }
}
.AccordeonItem__header__title {
  color: #FFF;
  font-family: "Inter";
  font-size: 30px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
}
@media (max-width: 1500px) {
  .AccordeonItem__header__title {
    max-width: 400px;
    font-size: 20px;
  }
}
@media (max-width: 1200px) {
  .AccordeonItem__header__title {
    max-width: 400px;
    font-size: 20px;
  }
}
@media (max-width: 600px) {
  .AccordeonItem__header__title {
    max-width: 300px;
    font-size: 22px;
  }
}
@media (max-width: 500px) {
  .AccordeonItem__header__title {
    max-width: 200px;
    font-size: 18px;
  }
}
.AccordeonItem__header__icon {
  background: url("../../../../assets/landing/images/arrow-accord.svg") no-repeat center center/cover;
  width: 50px;
  height: 50px;
  margin-left: 10px;
}
@media (max-width: 1500px) {
  .AccordeonItem__header__icon {
    width: 40px;
    height: 40px;
  }
}
@media (max-width: 750px) {
  .AccordeonItem__header__icon {
    width: 35px;
    height: 35px;
  }
}

.AccordeonItem__body_deactive {
  height: 0px;
  max-width: 1000px;
  width: 100%;
  transition: all 0.1s;
  border-bottom-left-radius: 24px;
  border-bottom-right-radius: 24px;
  background: rgba(255, 255, 255, 0.08);
  color: #FFF;
  font-family: "Inter";
  font-size: 0px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.AccordeonItem__body_active {
  height: auto;
  transition: all 0.4s;
  max-width: 1000px;
  padding: 35px 45px;
  width: 100%;
  border-bottom-left-radius: 24px;
  border-bottom-right-radius: 24px;
  border: 2px solid rgba(255, 255, 255, 0.12);
  background: rgba(255, 255, 255, 0.08);
  color: #FFF;
  font-family: "Inter";
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
@media (max-width: 1500px) {
  .AccordeonItem__body_active {
    font-size: 18px;
  }
}
@media (max-width: 600px) {
  .AccordeonItem__body_active {
    font-size: 16px;
  }
}
@media (max-width: 500px) {
  .AccordeonItem__body_active {
    font-size: 14px;
  }
}

.AccordeonItem__header_show {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 24px;
  border: 2px solid rgba(255, 255, 255, 0.12);
  border-bottom: 2px solid rgba(255, 255, 255, 0.438);
  background: rgba(255, 255, 255, 0.0784313725);
  padding: 35px 45px;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}/*# sourceMappingURL=AccordeonItem.css.map */