  }
}
.ChangeStrategy__dropdown-item_active::after {
  content: "";
  position: absolute;
  right: 14px;
  top: 12px;
  width: 20px;
  height: 20px;
  background: url("../../../../assets/account/images/galochka.svg") no-repeat center center/cover;
}
@media (max-width: 500px) {
  .ChangeStrategy__dropdown-item_active::after {
    width: 12px;
    height: 12px;
  }
}
@media (max-width: 1670px) {
  .ChangeStrategy__dropdown-item_active {
    max-width: 635px;
  }
}

.ChangeStrategy__body__speed__title {
  margin-top: 145px;
  color: #2B3674;
  text-align: center;
  font-family: "Open Sans";
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.36px;
}
.ChangeStrategy__body__speed__speedTest {
  margin-top: 25px;
}

.ChangeStrategy__blue-buttons {
  max-width: 283px;
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 16px;
}
.ChangeStrategy__blue-buttons > div {
  margin-left: 23px;
}
.ChangeStrategy__blue-buttons > div:first-child {
  margin-left: 0px;
}

.dropdown-item:focus {
  background-color: initial;
