.Title__title {
  color: #FFF;
  font-family: "Open Sans";
  font-size: 50px;
  font-weight: 800;
  line-height: normal;
  text-align: center;
}
@media (max-width: 2100px) {
  .Title__title {
    font-size: 44px;
  }
}
@media (max-width: 1840px) {
  .Title__title {
    font-size: 38px;
  }
}
@media (max-width: 1550px) {
  .Title__title {
    font-size: 34px;
  }
}
@media (max-width: 1450px) {
  .Title__title {
    font-size: 30px;
  }
}
@media (max-width: 1000px) {
  .Title__title {
    font-size: 26px;
  }
}
@media (max-width: 800px) {
  .Title__title {
    font-size: 22px;
  }
}
@media (max-width: 620px) {
  .Title__title {
    font-size: 18px;
  }
}/*# sourceMappingURL=Title.css.map */