.Theme {
  background: linear-gradient(135deg, #868CFF 0%, #4318FF 100%);
  border: 1px solid #71BEF4;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  cursor: pointer;
}

.Theme-state {
  width: 18px;
  height: 18px;
  background: url("../../../../assets/account/images/moon-theme.svg") no-repeat center center/cover;
}/*# sourceMappingURL=Theme.css.map */