.BalanceHistory {
  padding-left: 31px;
  padding-top: 29px;
  padding-right: 32px;
  padding-bottom: 20px;
  width: 100%;
  margin-top: 20px;
  background-color: var(--background-color-four);
  border-radius: 20px;
  box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
}
@media (max-width: 1400px) {
  .BalanceHistory {
    max-width: 100%;
    margin-left: 0px;
  }
}

.BalanceHistory__body {
  margin-top: 23px;
  overflow-x: auto;
  padding-bottom: 10px;
}
.BalanceHistory__body::-webkit-scrollbar {
  height: 12px;
  border-radius: 10px;
}
.BalanceHistory__body::-webkit-scrollbar-thumb {
  background: var(--scrollbar-color-line);
  border-radius: 10px;
}
.BalanceHistory__body::-webkit-scrollbar-track {
  background: var(--background-color-four);
}
.BalanceHistory__body__table__head {
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--line-one);
  padding-bottom: 8px;
}
.BalanceHistory__body__table__head__item {
  display: flex;
  align-items: center;
  width: 100%;
}
@media (max-width: 958px) {
  .BalanceHistory__body__table__head__item {
    min-width: 200px;
  }
}
.BalanceHistory__body__table__head__item__text {
  color: var(--nav-list-disable);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.28px;
}
.BalanceHistory__body__table__head__item__icon {
  background: url("../../../../assets/account/images/arrow-down-list.svg") no-repeat center center/cover;
  width: 24px;
  height: 24px;
  margin-left: 7px;
}

.__select {
  position: relative;
  width: 230px;
  height: 40px;
  margin: 0 auto;
}
.__select[data-state=active] .__select__title::before {
  transform: translate(-3px, -50%) rotate(-45deg);
}
.__select[data-state=active] .__select__title::after {
  transform: translate(3px, -50%) rotate(45deg);
}
.__select[data-state=active] .__select__content {
  opacity: 1;
}
.__select[data-state=active] .__select__label + .__select__input + .__select__label {
  max-height: 40px;
  border-top-width: 1px;
}

.__select__title {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 8px 16px;
  border-radius: 5px;
  border: solid 1px #c7ccd1;
  cursor: pointer;
}
.__select__title::before, .__select__title::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 16px;
  display: block;
  width: 10px;
  height: 2px;
  transition: all 0.3s ease-out;
  background-color: #333333;
  transform: translate(-3px, -50%) rotate(45deg);
}
.__select__title::after {
  transform: translate(3px, -50%) rotate(-45deg);
}
.__select__title:hover {
  border-color: #D8093A;
}
.__select__title:hover::before, .__select__title:hover::after {
  background-color: #D8093A;
}

.reset {
  display: flex;
  width: 230px;
  padding: 8px 16px;
  margin: 0 auto;
  margin-bottom: 10px;
  border: solid 1px #c7ccd1;
  border-radius: 5px;
  transition: all 0.2s ease-out;
  cursor: pointer;
  font-weight: bold;
  background-color: #ffffff;
  color: #333333;
}
.reset:hover {
  background-color: #D8093A;
  color: #ffffff;
}

.__select__content {
  position: absolute;
  top: 40px;
  left: 3px;
  display: flex;
  flex-direction: column;
  width: calc(100% - 6px);
  background-color: #ffffff;
  border: 1px solid #c7ccd1;
  border-top: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  transition: all 0.3s ease-out;
  opacity: 0;
  z-index: 8;
}

.__select__input {
  display: none;
}
.__select__input:checked + label {
  background-color: #dedede;
}
.__select__input:disabled + label {
  opacity: 0.6;
  pointer-events: none;
}

.__select__label {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  max-height: 0;
  padding: 0 16px;
  transition: all 0.2s ease-out;
  cursor: pointer;
  overflow: hidden;
}
.__select__label + input + .__select__label {
  border-top: 0 solid rgba(199, 204, 209, 0.3764705882);
}
.__select__label:hover {
  background-color: #D8093A !important;
  color: #ffffff;
}

.BalanceHistory__bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 25px;
}
@media (max-width: 500px) {
  .BalanceHistory__bottom {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

.BalanceHistory__bottom__pagination {
  display: flex;
  align-items: center;
}
@media (max-width: 500px) {
  .BalanceHistory__bottom__pagination {
    margin-top: 20px;
  }
}
.BalanceHistory__bottom__pagination_arrow-left {
  background: url("../../../../assets/account/images/arrow-down-list.svg") no-repeat center center/cover;
  transform: rotate(90deg);
  width: 15px;
  height: 15px;
  cursor: pointer;
}
@media (max-width: 500px) {
  .BalanceHistory__bottom__pagination_arrow-left {
    width: 35px;
    height: 35px;
  }
}
.BalanceHistory__bottom__pagination_arrow-right {
  background: url("../../../../assets/account/images/arrow-down-list.svg") no-repeat center center/cover;
  width: 15px;
  height: 15px;
  cursor: pointer;
  transform: rotate(-90deg);
}
@media (max-width: 500px) {
  .BalanceHistory__bottom__pagination_arrow-right {
    width: 35px;
    height: 35px;
  }
}
.BalanceHistory__bottom__pagination__body {
  display: flex;
  align-items: center;
}
.BalanceHistory__bottom__pagination__body__item {
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
  color: var(--nav-list-disable);
  margin-left: 15px;
}
@media (max-width: 500px) {
  .BalanceHistory__bottom__pagination__body__item {
    font-size: 24px;
  }
}

.BalanceHistory__bottom__pagination__body__item_active {
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  color: var(--text-color-secondary);
  margin-left: 10px;
}
@media (max-width: 500px) {
  .BalanceHistory__bottom__pagination__body__item_active {
    font-size: 24px;
  }
}/*# sourceMappingURL=BalanceHistory.css.map */