.HistoryItem{
    &:first-child{
        margin-top: 8px;
    }
    &__row{
        display: flex;
        align-items: center;
        background-color: var(--background-color-four);
        &:hover{
            background: var(--background-color-three);
            box-shadow: -50px 0 0 0 var(--bg-light), 0 0 0 0 var(--bg-light), 200px 0 0 0 var(--bg-light), 400px 0 0 0 var(--bg-light), 600px 0 0 0 var(--bg-light), 800px 0 0 0 var(--bg-light), 1000px 0 0 0 var(--bg-light);
        }
        &__cell{
            &:first-child{
                padding-left: 5px;
            }
            width: 100%;
            @media(max-width: 958px) {
                min-width: 200px;
            }
            h4{
                color: var(--blue-first);
                font-size: 14px;
                font-style: normal;
                font-weight: 700;
                line-height: 24px; 
                letter-spacing: -0.28px;
                padding: 9px 0px;
                margin-bottom: 0px;
            }   
        }
    }
}