.EditStatus {
  width: 360px;
  background-color: var(--background-color-four);
  padding: 24px 16px;
  padding-top: 0px;
  border-radius: 20px;
  margin-left: 53px;
}
@media (max-width: 1860px) {
  .EditStatus {
    margin-left: 0px;
    margin-top: 40px;
    width: 800px;
  }
}
@media (max-width: 1150px) {
  .EditStatus {
    width: 500px;
  }
}
@media (max-width: 550px) {
  .EditStatus {
    width: 300px;
  }
}
.EditStatus__head {
  display: flex;
  align-items: center;
}
.EditStatus__head .arrow-close-button {
  border: 1px solid #D0D5DD;
  border-radius: 4px;
}
.EditStatus__head .arrow-close-button__icon {
  background: url("../../../../assets/account/images/arrow-close.svg") no-repeat center center/cover;
  width: 16px;
  height: 16px;
  padding: 8px;
}
.EditStatus__head h2 {
  color: var(--text-color-secondary);
  white-space: nowrap;
  font-family: "Manrope";
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-left: 8px;
  margin-bottom: 0px;
}
.EditStatus__name-status {
  margin-top: 24px;
  width: 100%;
}
.EditStatus__name-status h5 {
  color: var(--text-color-five);
  white-space: nowrap;
  font-family: "Manrope";
  font-size: 15px;
  font-style: normal;
  font-weight: 600;
  margin-bottom: 0px;
  line-height: normal;
}
.EditStatus__name-status__input {
  margin-top: 4px;
  width: 100%;
  border: 1px solid #D0D5DD;
  color: var(--text-color-five);
  border-radius: 4px;
  outline: none;
  padding: 10px 8px;
  background-color: var(--background-color-secondary);
}
.EditStatus__name-status .input-length {
  margin-top: 4px;
  color: #64748B;
  text-align: right;
  font-family: "Manrope";
  font-size: 14px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.EditStatus__color {
  margin-top: 24px;
  display: flex;
  align-items: center;
}
.EditStatus__color h4 {
  color: #64748B;
  font-family: "Manrope";
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  margin-bottom: 0px;
  line-height: normal;
}
.EditStatus__color__color {
  width: 16px;
  height: 16px;
  border-radius: 100%;
  margin-left: 8px;
  cursor: pointer;
}

.EditStatus__script {
  margin-top: 24px;
}
.EditStatus__script h3 {
  color: var(--text-color-five);
  font-family: "Manrope";
  font-size: 15px;
  font-style: normal;
  font-weight: 600;
  margin-bottom: 0px;
  line-height: normal;
}
.EditStatus__script__textArea {
  margin-top: 4px;
  width: 100%;
  border-radius: 4px;
  border: 1px solid #D0D5DD;
  padding: 12px 8px;
  resize: none;
  outline: none;
  color: var(--text-color-five);
  height: 115px;
  background-color: var(--background-color-secondary);
}
.EditStatus__script__textArea::-webkit-scrollbar {
  width: 4px;
  background-color: #f5f5f5;
  box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
}
.EditStatus__script__textArea::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: var(--blue-first);
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
.EditStatus__script__textArea:focus {
  outline: none;
}
.EditStatus__script__textArea::-moz-placeholder {
  color: #64748B;
  font-family: "Open Sans";
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: -0.3px;
}
.EditStatus__script__textArea::placeholder {
  color: #64748B;
  font-family: "Open Sans";
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: -0.3px;
}

.EditStatus__buttons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: 24px;
}
@media (max-width: 550px) {
  .EditStatus__buttons {
    flex-direction: column;
    align-items: center;
  }
}
.EditStatus__buttons__blue-button {
  border-radius: 4px;
  color: #FFF;
  white-space: nowrap;
  font-family: "Open Sans";
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  border-radius: 70px;
  line-height: 32px;
  letter-spacing: -0.3px;
  background-color: var(--ref-button);
  padding: 6px 10px;
  text-align: center;
  max-width: 100px;
  width: 100%;
  min-width: 70px;
  cursor: pointer;
  transition: all 0.4s;
  text-decoration: none;
  text-align: center;
  border: 1px solid var(--blue-button-border);
}
@media (max-width: 550px) {
  .EditStatus__buttons__blue-button {
    margin-left: 0px;
    margin-top: 15px;
  }
}
.EditStatus__buttons__blue-button:hover {
  background: var(--blue-button-hover);
  transition: all 0.4s;
}/*# sourceMappingURL=EditStatus.css.map */