.subTitle-cont{
    width: 100%;
    display: flex;
    justify-content: right;
    position: relative;
    margin-top: 60px;
    @media(max-width:2100px) {
        margin-top: 20px;
    }
    @media(max-width:1840px) {
        margin-top: 10px;
    }
    &::before{
        content: '';
        width: 720px;
        height: 720px;
        z-index: -1;
        
        border-radius: 720px;
        background: linear-gradient(180deg, #183398 0%, #322B54 70.14%);
        filter: blur(300px);
        position: absolute;
        left: 31%;
        top: -20%;
        @media(max-width:1100px) {
            width: 300px;
            height: 300px;
            filter: blur(200px);
        }
        @media(max-width:600px) {
            width: 100px;
            height: 100px;
            filter: blur(300px);
        }
        @media(max-width:320px) {
            width: 0px;
            height: 0px;
        }
    }
    
}
.subTitle{
    color: #FFF;
    font-family: 'Open Sans';
    font-size: 40px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-right: 122px;
    @media(max-width:2100px) {
        font-size: 30px;
    }
    @media(max-width:1840px) {
        font-size: 26px;
    }
    @media(max-width:1720px) {
        font-size: 24px;
    }
    @media(max-width: 1450px){
        font-size: 20px;
    }
    @media(max-width: 1250px){
        font-size: 18px;
    }
    @media(max-width:620px) {
        font-size: 25px;
        margin-right: 60px;
    }
    @media(max-width: 1200px) {
        font-size: 22px;
    }
    @media(max-width: 900px) {
        font-size: 20px;
    }
    @media(max-width: 450px) {
        font-size: 0px;
    }
}