.MyContacts {
  padding-left: 31px;
  padding-top: 29px;
  padding-right: 32px;
  padding-bottom: 20px;
  width: 100%;
  margin-top: 20px;
  background-color: var(--background-color-four);
  border-radius: 20px;
  box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
}
@media (max-width: 1400px) {
  .MyContacts {
    max-width: 100%;
    margin-left: 0px;
  }
}
.MyContacts__head {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 700px) {
  .MyContacts__head {
    flex-direction: column;
    justify-content: none;
  }
}
.MyContacts__head__block {
  display: flex;
  align-items: center;
  margin-top: 10px;
}
.MyContacts__head__block__buttons {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}
@media (max-width: 450px) {
  .MyContacts__head__block__buttons {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.MyContacts-icon {
  margin-left: 33px;
}
@media (max-width: 1000px) {
  .MyContacts-icon {
    margin-left: 10px;
  }
}/*# sourceMappingURL=MyContacts.css.map */