.AdvantagesItem{
    margin-left: 54px;
    display: flex;
    flex-direction: column;
    align-items: center;
    &:first-child{
        margin-left: 0;
    }
    @media(max-width:1360px) {
        margin-left: 27px;
    }
    @media(max-width:1140px) {
        margin-top: 53px;
        margin-left: 0px;
    }
}
.AdvantagesItem__main-block{
    margin-top: 60px;
    border-radius: 26px;
    border: 2px solid rgba(255, 255, 255, 0.12);
    background: rgba(255, 255, 255, 0.08);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 24px;
    padding-right: 24px;
    min-height: 290px;
    width: 592px;
    @media(max-width:1920px) {
        width: 500px;
    }
    @media(max-width:1660px) {
        width: 400px;
    }
    @media(max-width:1300px) {
        width: 350px;
    }
    @media(max-width:1140px) {
        width: 100%;
    }
   
    
    padding-bottom: 23px;
    h2{
        margin-top: 45px;
        font-size: 29px;
        font-weight: 800;
        text-align: center;
        
        @media(max-width:1920px) {
            font-size: 26px;
        }
        @media(max-width:1660px) {
            font-size: 18px;
        }
        @media(max-width:1300px) {
            font-size: 22px;
            max-width: 180px;
        }
        @media(max-width:1140px) {
            font-size: 22px;
            max-width:  initial;
        }
    }
    h5{
        color: #FFF;
        font-family: 'Open Sans';
        font-size:24px;
        text-align: center;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        max-width: 592px;
        width: 100%;
        margin-top: 45px;
        @media(max-width:1920px) {
            font-size: 22px;
        }
        @media(max-width:1660px) {
            font-size: 18px;
        }
        @media(max-width:1140px) {
            font-size: 22px;
        @media(max-width: 600px) {
            font-size: 18px;
        }
        }
    }
}
.AdvantagesItem__img{
    width: 240px;
    height: 240px;
    img{
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center center;
    }
    @media(max-width: 1660px) {
        width: 220px;
        height: 220px;
    }
    @media(max-width:600px) {
        height: 180px;
        width: 180px;
    }
}
.AdvantagesItem__img_low{
    width: 270px;
    height: 270px;
    position: relative;
    img{
        width: 100%;
        object-fit: contain;
        object-position: center center;
    }
    &::before{
        content: '';
        max-width: 372px;
        width: 100%;
        height: 304px;
        border-radius: 372px;
        opacity: 0.45;
        background: linear-gradient(180deg, #C173FF 0%, #7471FF 100%);
        z-index: -1;
        filter: blur(200px);
        position: absolute;
        left: 0;
        top: 0;
    }
    @media(max-width: 1660px) {
        width: 215px;
        height: 215px;
    }
    @media(max-width:600px) {
        height: 150px;
        width: 150px;
    }
}
.AdvantagesItem__img_mid{
    width: 339px;
    position: relative;
    height: 339px;
    img{
        width: 100%;
        object-fit: contain;
        object-position: center center;
    }
    &::before{
        content: '';
        width: 272.387px;
        height: 272.387px;
        transform: rotate(15.541deg);
        background: linear-gradient(180deg, #EAC7FF 0%, #6373FF 70.14%);
        z-index: -1;
        filter: blur(400px);
        position: absolute;
        right: 100px;
        top: 80%;
    }
    @media(max-width: 1660px) {
        width: 215px;
        height: 215px;
    }
    @media(max-width:600px) {
        height: 150px;
        width: 150px;
    }
}
.AdvantagesItem__img_high{
    width: 339px;
    height: 339px;
    position: relative;
    img{
        width: 100%;
        object-fit: contain;
        object-position: center center;
    }
    &::before{
        content: '';
        width: 342.387px;
        height: 342.387px;
        transform: rotate(15.541deg);
        flex-shrink: 0;
        background: linear-gradient(180deg, #EAC7FF 0%, #6373FF 70.14%);
        filter: blur(300px);
        z-index: -1;
        position: absolute;
        right: 100px;
        top: 0;
    }
    @media(max-width: 1660px) {
        width: 230px;
        height: 230px;
    }
    @media(max-width:600px) {
        height: 150px;
        width: 150px;
    }
}