.Managament {
  background: url("../../../../assets/account/images/prm-background-fuul.png") no-repeat center center/cover;
  border-bottom-left-radius: 200px;
  position: relative;
}
@media (max-width: 700px) {
  .Managament {
    display: none;
  }
}
.Managament_wrapper {
  padding-top: 435px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (max-width: 1000px) {
  .Managament_wrapper {
    max-width: 1000px;
    width: 100%;
    padding-top: 250px;
  }
}

.img-container {
  max-width: 580px;
  width: 70%;
}
.img-container img {
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: center center;
     object-position: center center;
}

.partner-buttom {
  color: #FFF;
  font-family: DM Sans;
  font-size: 17.58px;
  font-weight: 500;
  line-height: 100%;
  margin-top: 130px;
  border-radius: 26.37px;
  text-align: center;
  border: 2.198px solid rgba(255, 255, 255, 0.2);
  padding-top: 23px;
  padding-bottom: 23px;
  max-width: 471px;
  width: 90%;
  cursor: pointer;
}

.bottom-bar {
  display: flex;
  align-items: center;
  margin-top: 400px;
  padding-bottom: 20px;
}
@media (max-width: 1200px) {
  .bottom-bar {
    margin-top: 600px;
  }
}
@media (max-width: 1000px) {
  .bottom-bar {
    margin-top: 300px;
  }
}
.bottom-bar__group {
  display: flex;
  margin-left: 45px;
}
@media (max-width: 1350px) {
  .bottom-bar__group {
    flex-direction: column;
  }
}
@media (max-width: 1000px) {
  .bottom-bar__group {
    flex-direction: row;
  }
}
@media (max-width: 570px) {
  .bottom-bar__group {
    flex-direction: column;
  }
}
.bottom-bar__group:first-child {
  margin-left: 0px;
}
.bottom-bar__group__item {
  color: #FFF;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.28px;
  margin-left: 45px;
}
.bottom-bar__group__item:first-child {
  margin-left: 0px;
}
@media (max-width: 1350px) {
  .bottom-bar__group__item {
    margin-left: 0;
    margin-top: 10px;
  }
}
@media (max-width: 1000px) {
  .bottom-bar__group__item {
    margin-left: 45px;
  }
}
@media (max-width: 570px) {
  .bottom-bar__group__item {
    margin-left: 0px;
  }
}

.Theme-container {
  position: absolute;
  bottom: 15px;
  right: 23px;
}/*# sourceMappingURL=Managament.css.map */