.Motivator {
  background: url("../../../../assets/account/images/roket.png") no-repeat center right/cover;
  min-width: 100%;
  min-height: 350px;
  background-repeat: 20px;
  padding-left: 64px;
  padding-top: 89px;
  padding-bottom: 56px;
  border-radius: 20px;
  margin-top: 26px;
}
@media (max-width: 1300px) {
  .Motivator {
    background: url("../../../../assets/account/images/roket.png") no-repeat center left/cover;
  }
}
@media (max-width: 480px) {
  .Motivator {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-left: 10px;
    padding-right: 10px;
  }
}
.Motivator__title {
  font-size: 34px;
  font-style: normal;
  font-weight: 700;
  line-height: 42px;
  letter-spacing: -0.68px;
  color: var(--text-color-first);
}
@media (max-width: 480px) {
  .Motivator__title {
    text-align: center;
  }
}
.Motivator__subTitle {
  margin-top: 10px;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px;
  letter-spacing: -0.32px;
  color: var(--text-color-four);
}
@media (max-width: 480px) {
  .Motivator__subTitle {
    text-align: center;
  }
}
.Motivator__buttons {
  display: flex;
  align-items: center;
  margin-top: 38px;
}
@media (max-width: 480px) {
  .Motivator__buttons {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
.Motivator__buttons__button-white {
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  padding: 11px 24px;
  letter-spacing: -0.28px;
  border-radius: 16px;
  color: var(--text-color-secondary);
  cursor: pointer;
  background: var(--market-place-button-bg);
  transition: all 0.3s;
}
.Motivator__buttons__button-white:hover {
  background: var(--marketPlaceButton);
  color: var(--text-color-first);
  transition: all 0.3s;
}
.Motivator__buttons__button {
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  cursor: pointer;
  border-radius: 16px;
  padding: 11px 24px;
  background: transparent;
  line-height: 28px;
  letter-spacing: -0.32px;
  color: var(--text-color-four);
  margin-left: 38px;
  transition: all 0.3s;
}
.Motivator__buttons__button:hover {
  background-color: var(--blue-button-hover);
  transition: all 0.3s;
}
@media (max-width: 480px) {
  .Motivator__buttons__button {
    margin-top: 25px;
    margin-left: 0px;
  }
}/*# sourceMappingURL=Motivator.css.map */