.BlueButton {
  padding: 6px 20px;
  background: var(--ref-button);
  border-radius: 70px;
  min-width: 106px;
  cursor: pointer;
  transition: all 0.4s;
  text-decoration: none;
  color: #FFF;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.28px;
  border: 1px solid var(--blue-button-border);
}
.BlueButton:hover {
  background: var(--blue-button-hover);
  transition: all 0.4s;
}/*# sourceMappingURL=BlueButton.css.map */