.Popup {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 120;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(124, 124, 124, 0.5568627451);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
}
.Popup__content {
  max-width: 665px;
  min-height: 313px;
  width: 100%;
  background-color: var(--background-color-first);
  border-radius: 20px;
  box-shadow: 14px 17px 40px 4px rgba(112, 144, 176, 0.26);
}

.Popup__head {
  width: 100%;
  display: flex;
  justify-content: center;
  position: relative;
  top: -80px;
  border-radius: 100%;
  overflow: hidden;
}

.Popup__body {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  top: -80px;
  margin-top: 30px;
  color: var(--apex-side-one);
  text-align: center;
  font-family: "Open Sans", sans-serif;
  font-size: 31px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 77.419% */
  letter-spacing: -0.62px;
}

.Popup__buttons {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-around;
}

.popup-active {
  height: 100vh;
  overflow: hidden;
}/*# sourceMappingURL=Popup.css.map */