.NextGeneration {
  width: 100%;
}

.blob-blocks {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  margin-top: 125px;
}
@media (max-width: 1300px) {
  .blob-blocks {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
.blob-blocks .blob-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 555px;
  width: 100%;
  position: relative;
}
@media (max-width: 1300px) {
  .blob-blocks .blob-block {
    margin-top: 150px;
  }
}
.blob-blocks .blob-block:first-child {
  margin-top: 0px;
}
.blob-blocks .blob-block .blob-contain {
  position: relative;
}
.blob-blocks .blob-block .blob-contain::before {
  content: "";
  position: absolute;
  width: 65%;
  height: 18%;
  left: -40%;
  bottom: 0;
  z-index: 120;
  background: url("../../../../assets/landing/images/open-ai.png") no-repeat center center/cover;
}
.blob-blocks .blob-block .blob-contain::after {
  content: "";
  position: absolute;
  border-radius: 536px;
  background: linear-gradient(180deg, #183398 0%, #322B54 70.14%);
  filter: blur(300px);
  z-index: -1;
  width: 536px;
  height: 536px;
  left: 0px;
  top: 0px;
}
@media (max-width: 1600px) {
  .blob-blocks .blob-block .blob-contain::after {
    width: 300px;
    height: 300px;
  }
}
@media (max-width: 1100px) {
  .blob-blocks .blob-block .blob-contain::after {
    width: 250px;
    height: 250px;
    filter: blur(200px);
  }
}
@media (max-width: 500px) {
  .blob-blocks .blob-block .blob-contain::after {
    width: 150px;
    height: 150px;
  }
}
.blob-blocks .blob-block h1 {
  margin-top: 95px;
  text-align: center;
  font-weight: 800;
}
.blob-blocks .blob-block h3 {
  margin-top: 55px;
  color: #FFF;
  font-family: "Open Sans";
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: 40px;
  max-width: 435px;
  width: 100%;
  text-align: center;
}
@media (max-width: 1400px) {
  .blob-blocks .blob-block h3 {
    font-size: 20px;
    line-height: 30px;
  }
}
@media (max-width: 700px) {
  .blob-blocks .blob-block h3 {
    font-size: 20px;
    line-height: 30px;
  }
}

#voice::before {
  background: url("../../../../assets/landing/images/voice-prm.png") no-repeat center center/cover;
  width: 45%;
  left: -20%;
  height: 45%;
}

.NextGeneration__title {
  font-size: 26px;
}

.NextGeneration_marquee-item {
  margin-left: 5vw;
}/*# sourceMappingURL=NextGeneration.css.map */