.FAQ{
    position: relative;
    &_marque-item{
        margin-left: 95px;
    }
    &::before{
        content: '';
        background: linear-gradient(180deg, #EAC7FF 0%, #6373FF 70.14%);
        filter: blur(300px);
        width: 352.61px;
        height: 352.61px;
        position: absolute;
        transform: rotate(15.541deg);
        left: 0px;
        top: 180px;
        z-index: -1;
        @media(max-width:450px) {
            width: 0px;
            height: 0px;
        }
    }
    &::after{
        content: '';
        background: linear-gradient(180deg, #EAC7FF 0%, #6373FF 70.14%);
        filter: blur(300px);
        width: 352.61px;
        height: 352.61px;
        position: absolute;
        transform: rotate(15.541deg);
        right: 200px;
        bottom: 180px;
        z-index: -1;
        @media(max-width:450px) {
            width: 150px;
            height: 150px;
        }
    }
}

