.landing{
    background: #000;
    overflow-x: hidden;
}
.k1{
    color: #FFF;
    text-align: center;
    font-family: 'Open Sans';
    font-size: 39px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    max-width: 1618px;
    width: 100%;
    margin: 0px auto;
    margin-top: 200px;
    @media(max-width: 2100px){
        font-size: 32px;
        margin-top: 150px;
    }
    @media(max-width: 1840px) {
        font-size: 28px;
        margin-top: 130px;
    }
    @media(max-width:1840px) {
        margin-top: 115px;
        font-size: 26px;
    }
    @media(max-width: 1550px){
        margin-top: 100px;
        font-size: 24px;
    }
    @media(max-width: 1450px){
        font-size: 20px;
        margin-top: 80px;
    }
    @media(max-width: 1250px){
        font-size: 20px;
    }
    @media(max-width: 1000px){
        padding-left: 15px;
        padding-right: 15px;
    }
    @media(max-width: 700px){
        font-size: 16px;
    }
    
}