.PrevPage{
    position: absolute;
    left: 30px; top: 49px;
    display: flex;
    align-items: center;
    cursor: pointer;
    &__icon {
        background: url('../../../../assets/account/images/arrow-prev-reg-pages.svg') no-repeat center center / cover;
        width: 15px;
        height: 20px;
    }
    &__text{
        transition: all 0.3s;
        color: var(--nav-list-disable);
        font-family: 'DM Sans', sans-serif;
        font-size: 14px;
        font-weight: 500;
        line-height: 20px;
        letter-spacing: -0.28px;
        margin-left: 10px;
    }
   
}
.PrevPage:hover {
    .PrevPage__text{
        color: var(--nav-list-disable);
    }
    .PrevPage__icon{
        filter: brightness(0) saturate(100%) invert(12%) sepia(42%) saturate(4592%) hue-rotate(226deg) brightness(92%) contrast(80%);
    }
}