.LandingRefPartner{
    
}

.LandingRefPartner__avatarAndInfo{
    display: flex;
    justify-content: center;
    margin-top: 40px;
    @media(max-width: 1000px) {
        flex-direction: column;
        align-items: center;
    }
    &__avatar{
        width: 640px;
        border-radius: 637px;
        position: relative;
        @media(max-width: 2300px){
            width: 580px;
        }
        @media(max-width: 2000px){
            width: 480px;
        }
        @media(max-width: 1500px){
            width: 350px;
        }
        @media(max-width: 1000px){
            width: 300px;
        }
        @media(max-width:600px) {
            width: 300px;
        }
        @media(max-width:450px) {
            width: 200px;
        }
        &::before{
            content: '';
            position: absolute;
            top: 0;left: 0;
            border-radius: 720px;
            background: linear-gradient(180deg, #98AEFF 0%, #7C46ED 70.14%);
            filter: blur(300px);
            width: 300px;
            height: 300px;
        }
        img{
            width: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
    &__person{
        margin-left: 74px;
        @media(max-width: 1000px) {
            margin-left: 0px;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        h5{
            color: #FFF;
            font-family: 'Open Sans';
            font-size: 78px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
            @media(max-width: 2000px){
               font-size: 54px;
            }
            @media(max-width: 1500px){
                font-size: 40px;
            }
            @media(max-width:600px) {
                font-size: 36px;
            }
            @media(max-width:450px) {
                font-size: 32px;
            }
        }
        h6{
            color: #FFF;
            font-family: 'Open Sans';
            font-size: 40px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
            @media(max-width: 2000px){
                font-size: 26px;
            }
            @media(max-width: 1500px){
                font-size: 24px;
            }
            @media(max-width:600px) {
                font-size: 22px;
            }
            @media(max-width:450px) {
                font-size: 18px;
            }
        }
        &__info{
            margin-top: 35px;
            min-height: 260px;
            border-radius: 26px;
            border: 2px solid rgba(255, 255, 255, 0.12);
            background: rgba(255, 255, 255, 0.08);
            position: relative;
            width: 915px;
            @media(max-width: 2000px){
                width: 500px;
            }
            @media(max-width:1500px) {
                width: 500px;
            }
            @media(max-width:550px) {
                width: 380px;
            }
            @media(max-width:450px) {
                width: 290px;
            }
            &__text{
                color: #FFF;
                font-family: 'Open Sans';
                font-size: 40px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;
                padding: 34px 28px;
                @media(max-width: 2000px){
                    font-size: 22px;
                }
                @media(max-width:1500px) {
                    font-size: 16px;
                }
                @media(max-width:600px) {
                    font-size: 14px;
                }
            }
        }
    }
   
}

.LandingRefPartner__subTitle{
    text-align: center;
    font-family: 'Open Sans';
    font-size: 49px;
    font-style: normal;
    font-weight: 800;
    line-height: 70px; 
    text-transform: uppercase;
    margin-top: 50px;
    @media(max-width: 2000px) {
        font-size: 36px;
        line-height: 55px;
        margin-top: 100px;
    }
    @media(max-width:1000px) {
        font-size: 30px;
    }
    @media(max-width:600px) {
        font-size: 24px;
        line-height: 40px;
    }
}
.LandingRefPartner__blob-container{
    position: absolute;
    right: 0px;
    bottom: -30%;
    @media(max-width:1350px) {
        bottom: -20%;
    }
}
.LandingRefPartner__marquee-item{
    margin-left: 5vw;
}

.socials{
    width: 100%;
    justify-content: center;
    display: flex;
    z-index: 1;
    align-items: center;
    margin-top: 100px;
    @media(max-width: 1500px){
        margin-top: 50px;
    }
    @media(max-width:1050px) {
        flex-direction: column;
        align-items: center;
     }
    &__item{
        z-index: 0;
        margin-right: 130px;
        position: relative;
        @media(max-width:1220px) {
           margin-right: 70px;
        }
        @media(max-width:1050px) {
            margin-right: 0px;
            margin-top: 50px;
        }
        &:last-child{
            margin-right: 0px;
        }
        &::before{
            content: '';
            position: absolute;
            border-radius: 322px;
            left: 0;top: 0;
            z-index: -1;
            width: 322px;
            height: 322px;
            background: linear-gradient(180deg, #98AEFF 0%, #7C46ED 70.14%);
            filter: blur(300px);
            @media(max-width:900px){
                width: 160px;
                height: 160px;
            }
            @media(max-width:600px){
                width: 0px;
                height: 0px;
                display: none;
            }
        }
        h4{
            color: #FFF;
            text-align: center;
            font-family: 'Open Sans';
            font-size: 40px;
            @media(max-width: 1500px){
                font-size: 30px;
            }
            font-style: normal;
            font-weight: 400;
            margin-top: 6px;
            line-height: normal;
            @media(max-width:1350px) {
                font-size: 28px;
            }
        }
    }
}
.socials__icon{
    width: 200px;
    height: 200px;
    @media(max-width:1500px) {
        width: 140px;
        height: 140px;
    }
}

