.ProfileInfo{
    padding: 0px 17px;
    padding-top: 18px;
    padding-bottom: 35px;
    max-width: 552px;
    width: 100%;
    margin-top: 20px;
    background-color: var(--background-color-four);
    border-radius: 20px;
    box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
    @media(max-width: 1400px) {
        max-width: 100%;
    }
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &__head{
        width: 100%;
        border-radius: 16px;
        min-height: 131px;
        background: url('../../../../assets/account/images/avatar-background.png') no-repeat center right / cover;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        &__avatar-img{
            position: relative;
            bottom: -44px;
            width: 90px;
            height: 90px;
            border-radius: 100%;
            border: 4px solid #FFF;
            &::before{
                content: '';
                background: url('../../../../assets/account/images/avatar-psevdo-image.png') no-repeat center center / cover;
                width: 24px;
                height: 24px;
                position: absolute;
                right: -6px;
                bottom: -6px;
                cursor: pointer;
            }
            img{
                width: 100%;
                border-radius: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center center;
            }
        }
    }
    &__body{
        display: flex;
        flex-direction: column;
        align-items: center;
        &__text{
            margin-top: 56px;
            text-align: center;
        }
        &__tokenAndVisits{
            margin-top: 26px;
            display: flex;
            justify-content: space-between;
            max-width: 260px;
            width: 100%;
            &__item{
                margin-left: 62px;
                display: flex;
                flex-direction: column;
                align-items: center;
                &:first-child{
                    margin-left: 0px;
                }
                @media(max-width:350px){
                    margin-left: 20px;
                }
            }
        }
    }
}
.ProfileInfo__body__title{
    color: var(--blue-first);
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px; 
    letter-spacing: -0.4px;
    margin-bottom: 0px;
}
.ProfileInfo__body__subTitle{
    color: var(--nav-list-disable);
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: -0.28px;
    margin-top: 4px;
    margin-bottom: 0px;
}