.Title{
    &__title{
        color: #FFF;
        font-family: 'Open Sans';
        font-size: 50px;
        font-weight: 800;
        line-height: normal;
    
        text-align: center;
        @media(max-width:2100px) {
            font-size: 44px;
        }
        @media(max-width:1840px) {
            font-size: 38px;
        }
        @media(max-width: 1550px){
            font-size: 34px;
        }
        @media(max-width: 1450px){
            font-size: 30px;
        }
        @media(max-width:1000px) {
            font-size: 26px;
        }
        @media(max-width:800px) {
            font-size: 22px;
        }
        @media(max-width:620px) {
            font-size: 18px;
        }
    }
    
}