.FormInputAuth {
  margin-top: 32px;
}

.FormInputAuth__title {
  display: flex;
  align-items: center;
}
.FormInputAuth__title__text {
  color: var(--blue-first);
  font-family: "DM Sans", sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 100%;
  letter-spacing: -0.28px;
}
.FormInputAuth__title .important {
  color: var(--blue-first);
  font-family: "DM Sans", sans-serif;
  font-size: 14px;
  font-weight: 800;
  line-height: 100%;
  letter-spacing: -0.28px;
}

.FormInputAuth__inputCountainer {
  justify-content: space-between;
  position: relative;
  border: transparent;
  transition: border 0.2s;
  border: 1px solid var(--nav-list-disable);
  border-radius: 10px;
  height: 55px;
  width: 410px;
  transition: all 0.5s;
}
.FormInputAuth__inputCountainer:focus-within {
  border: 1px solid rgba(43, 54, 116, 0.5254901961);
}
@media (max-width: 1880px) {
  .FormInputAuth__inputCountainer {
    width: 300px;
  }
}

.FormInputAuth-input {
  color: var(--input-color);
  font-weight: 400;
  font-size: 16px;
  height: 48px;
  padding-left: 20px;
  padding-right: 20px;
  border: transparent;
  transition: border 0.2s;
  border-radius: 16px;
  background: transparent;
  outline: none;
  width: 100%;
}
.FormInputAuth-input::-moz-placeholder {
  color: var(--nav-list-disable);
  font-family: "DM Sans", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%;
  letter-spacing: -0.28px;
}
.FormInputAuth-input::placeholder {
  color: var(--nav-list-disable);
  font-family: "DM Sans", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%;
  letter-spacing: -0.28px;
}

.text_hide_auth {
  margin-top: 0px;
}

.text_hide_auth {
