.AccountFooter{
    display: flex;
    align-items: center;
    margin-top: 126px;
    width: 100%;
    justify-content: space-between;
    @media(max-width: 500px) {
        flex-direction: column-reverse;
        align-items: center;
        justify-content: none;
    }
    h3{
        margin-top: 0px;
        margin-bottom: 0px;
    }
}
.AccountFooter__list{
    display: flex;
    align-items: center;
    @media(max-width: 500px) {
        flex-direction: column;
        align-items: center;
    }
    li{
        margin-left: 43px;
        &:first-child{
            margin-left: 10px;
            @media(max-width: 500px) {
                margin-left: 0px;
            }
        }
        @media(max-width: 500px) {
            margin-left: 0px;
        }
    }
}
.AccountFooter__item{
    color: var(--nav-list-disable);
    font-family: 'DM Sans', sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px; 
    list-style: none;
    letter-spacing: -0.28px;
    padding-bottom: 10px;
}