.LandingEnd{
    margin-top: 101px;
    &__title{
        text-align: center;
        font-family: 'Open Sans';
        font-size: 58px;
        font-style: normal;
        font-weight: 800;
        line-height: 70px; 
        padding-bottom: 5px;
        text-transform: uppercase;
        @media(max-width:2000px) {
            font-size: 50px;
            line-height: 60px;
        }
        @media(max-width:1500px) {
            font-size: 42px;
            line-height: 50px;
        }
        @media(max-width:1200px) {
            font-size: 34px;
            line-height: 40px;
        }
        @media(max-width:850px) {
            font-size: 26px;
            line-height: 30px;
        }
        @media(max-width:730px) {
            font-size: 22px;
        }
        @media(max-width:550px) {
            font-size: 20px;
        }
        @media(max-width:400px) {
            font-size: 20px;
        }
    }
}
.blick-button-cont{
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: 100px;
   
}
.LandingEnd__footer{
    background: rgba(0, 0, 0, 0.30);
    margin-top: 122px;
    height: 201px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    @media(max-width:750px) {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    h2{
        color: #FFF;
        text-align: right;
        font-family: 'Open Sans';
        font-size: 22px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        @media(max-width:1000px) {
            font-size: 18px;
        }
        @media(max-width:750px) {
            text-align: center
        }
    }
    h3{
        color: #FFF;
        font-family: 'Open Sans';
        font-size: 22px;
        font-style: normal;
        margin-top: 15px;
        font-weight: 400;
        line-height: normal;
        @media(max-width:1000px) {
            font-size: 18px;
        }
        @media(max-width:750px) {
            text-align: center
        }
    }
    h4{
        color: #FFF;
        margin-top: 15px;
        text-align: right;
        font-family: 'Open Sans';
        font-size: 22px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        @media(max-width:1000px) {
            font-size: 18px;
        }
        @media(max-width:750px) {
            text-align: center
        }
    }
}