.CreateLanding{
    padding-left: 31px;
    padding-top: 20px;
    padding-right: 25px;
    padding-bottom: 41px;
    width: 100%;
    margin-top: 20px;
    background-color: var(--background-color-four);
    border-radius: 20px;
    box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
    @media(max-width: 1400px) {
        max-width: 100%;
        margin-top: 20px;
    }
}
.CreateLanding__body{
    display: flex;
    justify-content: space-between;
    @media(max-width: 1350px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

.CreateLanding__column__buttons{
    display: flex;
    align-items: center;
    margin-top: 75px;
    &:first-child{
        margin-left: 0px;
    }
    @media(max-width: 1350px) {
        margin-top: 0px;
    }
    &__button{
        max-width: 200px;
        width: 100%;
        margin-left: 14px;
        white-space: nowrap;
        &:first-child{
            margin-left: 0px;
        }
        @media(max-width: 1350px) {
            margin-left: 0px;
            margin-top: 25px;
        }
    }
    @media(max-width: 1350px) {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
}
