.AccountBurgerMenu_deactive {
  width: 200px;
  position: absolute;
  background: var(--background-color-four);
  min-height: 500px;
  top: 0px;
  left: -100vw;
  transition: all 0.3s;
}

.AccountBurgerMenu_active {
  width: 200px;
  position: absolute;
  background: var(--background-color-four);
  min-height: 550px;
  top: 0px;
  left: 0px;
  z-index: 99;
  transition: all 0.3s;
}

.AccountBurgerMenu__logo {
  background: url("../../../../assets/account/images/prm-logo-2.png") no-repeat center center/cover;
  max-width: 150px;
  width: 100%;
  height: 16px;
  margin-top: 56px;
  margin-left: 20px;
}

.AccountBurgerMenu__logo_dark {
  background: url("../../../../assets/account/images/prm4-logo-darkTheme.png") no-repeat center center/cover;
  max-width: 150px;
  width: 100%;
  height: 16px;
  margin-top: 56px;
  margin-left: 20px;
}

.AccountBurgerMenu__nav-list {
  margin-top: 40px;
  padding-left: 30px;
}

.AccountBurgerMenu__nav-list__item_active {
  display: flex;
  align-items: center;
  transition: all 0.2s;
  margin-top: 24px;
  border-right: 4px solid transparent;
  cursor: pointer;
  border-right: 4px solid var(--blue-first);
}
.AccountBurgerMenu__nav-list__item_active:first-child {
  margin-top: 0px;
}
.AccountBurgerMenu__nav-list__item_active .AccountBurgerMenu-icon {
  width: 20px;
  height: 20px;
  filter: var(--side-nav-bar-menu-icon-active);
}

.AccountBurgerMenu__nav-list__item {
  display: flex;
  align-items: center;
  transition: all 0.2s;
  margin-top: 24px;
  border-right: 4px solid transparent;
  cursor: pointer;
}
.AccountBurgerMenu__nav-list__item:hover {
  transition: all 0.2s;
  border-right: 4px solid var(--nav-list-hover);
  padding-right: 30px;
}
.AccountBurgerMenu__nav-list__item:hover h3 {
  color: var(--nav-list-hover);
  margin-top: 0px;
  margin-bottom: 0px;
}
.AccountBurgerMenu__nav-list__item:hover .SideNavMenu-icon {
  filter: var(--nav-list-icon-hover);
}
.AccountBurgerMenu__nav-list__item .AccountBurgerMenu-icon {
  width: 20px;
  height: 20px;
  filter: var(--side-nav-bar-menu-icon);
}

.AccountBurgerMenu__nav-item_link {
  color: var(--nav-list-disable);
  text-decoration: none;
  margin-left: 12px;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: -0.32px;
  margin-left: 12px;
  margin-top: 0px;
  margin-bottom: 0;
}

#nav-main {
  background: url("../../../../assets/account/images/main-icon.svg") no-repeat center center/cover;
}

#nav-finance {
  background: url("../../../../assets/account/images/finance-icon.svg") no-repeat center center/cover;
}

#nav-team {
  background: url("../../../../assets/account/images/team-icon.svg") no-repeat center center/cover;
}

#nav-contacts {
  background: url("../../../../assets/account/images/contacts-icon.svg") no-repeat center center/cover;
}

#nav-marketplace {
  background: url("../../../../assets/account/images/marketplace-icon.svg") no-repeat center center/cover;
}

#nav-token {
  background: url("../../../../assets/account/images/token.svg") no-repeat center center/cover;
}

#nav-profile {
  background: url("../../../../assets/account/images/profile.svg") no-repeat center center/cover;
}

#nav-sign-out {
  background: url("../../../../assets/account/images/sign-out-icon.svg") no-repeat center center/cover;
  width: 16px;
  height: 21px;
}/*# sourceMappingURL=AccountBurgerMenu.css.map */