.LoginForm {
  max-width: 410px;
  width: 100%;
  margin: 0px auto;
  margin-top: 227px;
}

.metamask-button {
  display: flex;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  border-radius: 16px;
  transition: all 0.4s;
  background: var(--background-color-secondary);
  width: 100%;
  padding: 7px 0px;
  margin-top: 36px;
}
.metamask-button:hover {
  background-color: var(--background-color-seven-hover);
  transition: all 0.4s;
}
.metamask-button__icon {
  background: url("../../../../assets/account/images/metaMaskActive.png") no-repeat center center/cover;
  width: 40px;
  height: 40px;
}
.metamask-button h3 {
  margin-bottom: 0px;
  color: var(--blue-first);
  font-family: "DM Sans", sans-serif;
  font-size: 14px;
  margin-left: 7px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: -0.28px;
}

.LoginForm__form__hr {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 26px;
}
.LoginForm__form__hr__line {
  height: 1px;
  background-color: var(--nav-list-disable);
  width: 40%;
}
.LoginForm__form__hr h4 {
  margin-bottom: 0px;
  color: var(--nav-list-disable);
  text-align: center;
  font-family: "DM Sans", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px; /* 171.429% */
  letter-spacing: -0.28px;
}

.Login__title, .Login__subTitle {
  text-align: left;
}

.LoginForm__form__checks {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 31px;
}
.LoginForm__form__checks h5 {
  color: var(--blue-first);
  font-family: "DM Sans", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: -0.28px;
  margin-top: 0px;
  cursor: pointer;
}
.LoginForm__form__checks__checkbox {
  display: flex;
  align-items: center;
}
.LoginForm__form__checks__checkbox h4 {
  margin-bottom: 0px;
  margin-left: 11px;
  color: var(--blue-secondary);
  font-family: "DM Sans", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.28px;
}/*# sourceMappingURL=LoginForm.css.map */