.BlickButton-cont{
    max-width: 556px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 24px;
    border: 4px solid #6A4BFF;
    background: transparent;
    height: 117px;
    cursor: pointer;
    position: relative;
    transition: all 0.2s;
    overflow: hidden;
    @media(max-width:1600px) {
        max-width: 480px;
    }
    @media(max-width:1320px) {
        max-width: 400px;
        height: 100px;
    }
    
    &::after{
        content: '';
        position: absolute;
        top: 0;left: 0;
        width: 415px;
        height: 226px;
        border-radius: 415px;
        background: linear-gradient(180deg, #69A7E0 0%, #A65DE0 70.14%);
        filter: blur(300px);
    }
    &::before{
        content: '';
        position: absolute;
        top: 0;left: 0;
        width: 25px;
        height: 100%;
        background-color: rgba(#FFF, 0.6);
        transform: translateX(-40px) skew(-20deg);
    }
    &:hover{
        height: 122px;
        transition: all 0.2s;
        max-width: 565px;
        @media(max-width:1600px) {
            max-width: 520px;
        }
        @media(max-width:1320px) {
            max-width: 520px;
        }
        @media(max-width:1250px) {
            max-width: 300px;
        }
       
        @media(max-width:720px) {
            height: 102px;
        }
        
        &::before{
            animation: move-blick 0.5s;
        }
        &::after{

        }
        .BlickButton{
            background: linear-gradient(180deg, #CF83FF 0%, #6A4BFF 100%);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            transition: all 0.3s;
        }
    }
}

.BlickButton{
    color: #FFF;
    transition: all 0.3s;
    font-family: 'Open Sans';
    font-size: 36px;
    font-weight: 800;
    @media(max-width:1600px) {
        font-size: 30px;
    }
    @media(max-width:1320px) {
        font-size: 24px;
    }
    @media(max-width:1000px) {
        font-size: 18px;
    }
    @media(max-width:720px) {
        font-size: 18px;
    }
}

@keyframes move-blick {
    from{
        transform: translateX(-40px) skew(-20deg);
    }
    to{
        transform: translateX(570px) skew(-20deg);
    }
}