.CreateLanding {
  padding-left: 31px;
  padding-top: 20px;
  padding-right: 25px;
  padding-bottom: 41px;
  width: 100%;
  margin-top: 20px;
  background-color: var(--background-color-four);
  border-radius: 20px;
  box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
}
@media (max-width: 1400px) {
  .CreateLanding {
    max-width: 100%;
    margin-top: 20px;
  }
}

.CreateLanding__body {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1350px) {
  .CreateLanding__body {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.CreateLanding__column__buttons {
  display: flex;
  align-items: center;
  margin-top: 75px;
}
.CreateLanding__column__buttons:first-child {
  margin-left: 0px;
}
@media (max-width: 1350px) {
  .CreateLanding__column__buttons {
    margin-top: 0px;
  }
}
.CreateLanding__column__buttons__button {
  max-width: 200px;
  width: 100%;
  margin-left: 14px;
  white-space: nowrap;
}
.CreateLanding__column__buttons__button:first-child {
  margin-left: 0px;
}
@media (max-width: 1350px) {
  .CreateLanding__column__buttons__button {
    margin-left: 0px;
    margin-top: 25px;
  }
}
@media (max-width: 1350px) {
  .CreateLanding__column__buttons {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}/*# sourceMappingURL=CreateLanding.css.map */