.ContactsTab{
    &__row{
        display: flex;
        @media(max-width: 1400px) {
            flex-direction: column;
        }
    }
}
.ContactsTab__chart-cont{
    padding-left: 31px;
    padding-top: 29px;
    padding-right: 32px;
    padding-bottom: 74px;
    width: 100%;
    background-color: var(--background-color-four);
    border-radius: 20px;
    margin-top: 20px;
    margin-left: 40px;
    box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
    @media(max-width: 1400px) {
        max-width: 100%;
        margin-left: 0px;
    }
    margin-left: 20px;
    &:first-child{
        margin-left: 0px;
    }
    
}