.FinancesPotencial{
    padding-left: 31px;
    padding-top: 29px;
    padding-right: 32px;
    padding-bottom: 74px;
    width: 100%;
    margin-top: 20px;
    background-color: var(--background-color-four);
    border-radius: 20px;
    box-shadow: 0px 4px 12px 0px rgba(8, 108, 201, 0.16);
    margin-left: 20px;
    @media(max-width: 1400px) {
        max-width: 100%;
        margin-left: 0px;
    }
}
.FinancesPotencial__body__profits{
    display: flex;
    align-items: center;
    margin-top: 36px;
    @media(max-width: 430px) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
.FinancesPotencial__body__buttons{
    display: flex;
    align-items: center;
    margin-top: 33px;
    @media(max-width: 430px) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
.FinancesPotencial__head{
    @media(max-width: 550px){
        flex-direction: column;
    }
}
.FinancesPotencial__body__company{
    @media(max-width: 430px) {
        display: flex;
        justify-content: center;
    }
}
.secondary-component-head{
    display: flex;
    align-items: center;
    
}
.financesPotencial__dropdown{
    margin-right: 30px;
}